@use 'assets/styles/shared' as *;

.container::-webkit-scrollbar {
  display: none;
}

.container {
  height: 65vh;
  overflow-y: scroll;
  overflow-x: hidden;

  .timelineHeader {
    background-color: $color-wild-sand;
    position: sticky;
    top: 0;
    z-index: 85;
    border: none;
  }

  [class*='rct-dateHeader'] {
    color: $color-fun-blue;
    font-weight: 600;
  }

  .buttonsWrap {
    display: inline-flex;
    gap: 10px;
    position: sticky;
    right: 0;

    .timelineButtons {
      background-color: $color-wild-sand;
      padding: 0.5em;
      border-radius: 4px;
    }
  }

  [class*='rct-day-0'],
  [class*='rct-day-6'] {
    background: rgba(182, 182, 182, 0.7) !important;
  }
}

.contextMenuWrap[class~='MuiMenuItem-root'] {
  width: 320px;
  justify-content: center;
  text-align: center;
}
