@use 'assets/styles/shared' as *;
@use "sass:color";

.holidayTable {
  width: auto;
  padding-right: rem(30px);
  margin-left: rem(35px);

  .holidayNavigation {
    display: flex;
    background-color: $color-wild-sand;
    margin-bottom: 5px;
    padding: 10px 0;

    .navigationItem {
      margin-left: 14px;

      font-size: rem(12px);
      font-weight: 600;
      color: $color-fun-blue;
    }
  }

  .search {
    margin: 5px;
  }

  .tableWidgets {
    background-color: $color-wild-sand;
    margin-bottom: 0.25em;
    display: flex;
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: rem(30px) rem(35px) 0;
  padding-bottom: rem(30px);

  .linkWrap {
    margin-top: 15px;
    margin-left: 15px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    > div {
      display: flex;
    }
  }

  .title {
    @include pageTitle;
    margin: 0;
  }
}

.legendHeading {
  color: $color-fun-blue;
  margin-left: rem(34px);
  font-size: rem(12px);
}

.legendWrap {
  position: fixed;
  bottom: 0;
  display: flex;
  padding: 1em 0;
  width: 100%;
  gap: 10px;
  color: $color-fun-blue;
  background-color: $color-wild-sand;

  .legend {
    font-size: rem(12px);
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 5px;

    .legendIcons {
      width: 15px;
      height: 15px;
    }

    .appliedVacation {
      background-color: #B7D8A0;
    }

    .confirmedVacation {
      background-color: #95BD79;
    }

    .plannedVacation {
      background-color: #EEF6E8;
    }
    .parentalLeave {
      background-color: #EE5F00;
    }
  }
}

.checkboxesWrap {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
}

:global {
  .rct-item {
    border: none !important;
     .rct-item-content {
      display: none !important;
     }
    &.requested {
      background-color: #B7D8A0 !important;
    }

    &.approved {
      background-color: #95BD79 !important;
    }

    &.planned {
      background-color: #EEF6E8 !important;
    }
    &.national-holiday {
      background-color: #ebdc979f !important;
    }
  }
  .rct-item.parental,
  .rct-item.maternity {
    background-color: #EE5F00 !important;
  }
}

.dropdown {
  background-color: $color-wild-sand !important;
}

.fieldTitle {
  color: $color-fun-blue;
}

.fieldWrap {
  background-color: $color-dr-white;
  position: relative;
  padding: 35px 10px;

  .removeButton {
    width: 20px;
    font-size: 25px;
    margin-bottom: 15px;
    margin-left: 10px;
    position: absolute;
    top: 5px;
    right: 5px;
  }
}

[class~='MuiAccordionDetails-root'] {
  padding: 0 !important;
}

.accordionWrap {
  background-color: $color-wild-sand !important;
}
