@use 'assets/styles/shared' as *;

.title {
  @include pageTitle;
}

.fullscreenModal {
  width: 90%;
  max-width: none;
  height: calc(100vh - 200px);
  bottom: 0;
  align-self: flex-end;
  margin: 0 0 30px 30px;
  padding: rem(10px);

  .title {
    font-size: 1rem;
    margin: 0 0 1rem;
  }
}

.dropdownWrap {
  padding: 0 30px;
  position: relative;
  top: 2em;
}

.imgContent {
  margin-left: 35px;
  padding: 1rem 0;
  display: flex;

  &.withTitle {
    height: 65vh;

    h2 {
      @include pageTitle;
      margin: 0;
      font-size: 1.2rem;
    }
  }
  .table{
    max-width: 70%;
    object-fit: contain;
    margin: 0 auto;
  }

  img {
    max-width: 100%;
    max-height: 100%;
  }
}

:global {
  .flex-layout {
    display: flex;
    align-items: flex-start;
    gap: 1rem;

    .col60 {
      width: 70%;
      svg {
        width: 100%;
        height: auto;
      }
      [class*='filterAndTableWrapper'] {
        padding: 0;
      }
    }
    .col40 {
      width: 30%;
      height: 600px;
    }
  }
}
