@use "sass:color";
@use 'assets/styles/shared' as *;

.messageWrap {
  position: relative;
}

.checkboxWrap {
  &[class~='MuiFormControl-root'] {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    column-gap: 10px;
  }
}

.checkbox {
  margin: auto;
}

.label {
  font-size: 12px;
  line-height: 1.42;
  font-weight: 400;
  text-align: left;
  cursor: auto;
}

.underline {
  &:hover {
    cursor: pointer;
    text-decoration: underline;
    text-decoration-color: #18469e;
  }
}

.crossOut {
  text-decoration: line-through;
  text-decoration-color: #18469e;

  &.label {
    &:hover {
      cursor: auto;
      text-decoration: line-through;
      text-decoration-color: #18469e;
    }
  }
}
