@use 'assets/styles/shared' as *;
@use "sass:color";

.header {
  justify-content: space-between;
  align-items: center;
  margin: rem(30px) rem(35px) 0;
  padding-bottom: rem(30px);

  .title {
    @include pageTitle;
    margin: 0;
  }

  .linkWrap {
    display: flex;
    margin-top: 15px;
  }
}

.content {
  margin: 0 rem(35px);
  display: flex;
  flex-direction: column;

  .headerBtnContainer {
    display: flex;
    flex-direction: row;
    justify-content: end;
    margin-bottom: 10px;
    margin-right: 20px;

    .headerBtn {
      margin-left: 5px;
      float: right;
    }

  }

  .detailContent {
    height: 75vh;
    display: flex;
    @media (max-width: 1000px) {
      display: block;
    }

    .detailMainWrap {
      display: flex;
      border-top-left-radius: 0.5em;
      border-bottom-left-radius: 0.5em;
      @media (max-width: 1000px) {
        display: block;
      }

      .detailMain {
        background-color: #f2f6ff;
        width: 0;
        opacity: 0;
        transition: width 400ms ease-in-out, opacity 400ms linear;
        overflow: auto;
        overflow-x: hidden;
        border-top-left-radius: 0.5em;
        border-bottom-left-radius: 0.5em;
        @media (max-width: 1000px) {
          height: 0;
          transition: height 400ms ease-in-out, opacity 400ms linear;
        }

        // placeholder for all not yet implemented modals
        //.historyBtn {
        //  display: none;
        //}

        .historyButton {
          display: flex;
          justify-content: flex-end;
        }

        .historyHolidays {
          display: block;
          border-color: rgb(110, 160, 81);
          &:hover {
            color: color.scale(rgb(110, 160, 81), $lightness: 25%);
            border-color: color.scale(rgb(110, 160, 81), $lightness: 25%);
          }
        }

        .historyWrap {
          padding: 0.5em 1em;
          border-bottom: 2px solid white;

          .historyHeader {
            display: flex;
            justify-content: space-between;
            align-items: center;

            h3 {
              font-size: 14px;
              font-weight: bold;
            }
          }
        }

        img {
          max-height: 125px;
          border-radius: 3px;
          @media screen and (max-width: 1200px) {
            max-height: 100px;
          }
          @media screen and (max-width: 1024px) {
            max-height: 75px;
          }
        }

        .detailHeader {
          font-size: 14px;
          padding: 1em;

          h2 {
            font-weight: 600;
          }
        }

        .detailMainInnerOverview {
          padding: 1em;
          display: grid;
          gap: .5em;
          border-bottom: 2px solid white;
          min-height: 293px;
          @media screen and (max-width: 1440px) {
            height: fit-content;
          }

          .detailBody {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;

            .dataWrap {
              .dataLabel {
                font-size: 14px;
                color: white;
              }
            }
          }

          .exportBtn {
            position: absolute;
            display: flex;
            justify-self: flex-end;
          }

          &.base {
            display: flex;
            flex-direction: row;
            background-color: #c7d0de;

          }

          &.contract {
            background-color: #c7c7c7;
          }

          &.absence {
            background-color: rgba(169, 209, 142, 0.8);
          }
        }

        .employeeTitle {
          display: flex;
          text-align: center;
          align-items: center;
          font-size: 13.5px;
          font-weight: 600;
          color: $color-fun-blue;
        }

        .detailBody {
          .dataWrap {
            padding: 0.25em 0;

            .dataValue {
              font-size: 14px;
              color: black;
            }

            .dataLabel {
              font-size: 14px;
              color: rgb(175, 175, 175);
            }

            .dataArray {
              display: flex;
              flex-direction: row;
              flex-wrap: wrap;
              font-size: 14px;
              color: black;
            }
          }
        }
      }

      .base {
        color: $color-fun-blue;
        background-color: transparent;

        .historyBtn {
          color: $color-fun-blue;
          border-color: $color-fun-blue;
        }
        .upload {
          float: left;
          border-color: $color-fun-blue;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          &:hover {

             color: color.scale($color-fun-blue, $lightness: 25%);
            //color: white;
            border-color: color.scale($color-gray, $lightness: 25%);
          }
        }
      }

      .contract {
        color: $color-gray;
        background-color: $color-wild-sand;

        .historyButton {
          color: $color-gray;
          border-color: $color-gray;
        }
        .upload {
          float: left;
          border-color: $color-gray;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          &:hover {
            color: color.scale($color-gray, $lightness: 25%);
            border-color: color.scale($color-gray, $lightness: 25%);
          }
        }
      }

      .absence {
        color: rgb(110, 160, 81);
        background-color: rgb(236, 253, 226);
        // .historyBtn {
        //   color: rgb(110, 160, 81);
        //   border-color: rgb(110, 160, 81);
        // }
        .history {
          font-size: rem(14px);
          border-color: rgb(110, 160, 81);
          float: right;
          &:hover {
            color: color.scale(rgb(110, 160, 81), $lightness: 25%);
          }
        }
        .upload {
          float: left;
          border-color: rgb(110, 160, 81);
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          &:hover {
            color: color.scale(rgb(110, 160, 81), $lightness: 25%);
            border-color: color.scale(rgb(110, 160, 81), $lightness: 25%);
          }
        }
      }

      // Dropdowns
      .accordionWrap {
        font-size: 12px;
        justify-content: space-between;
        background-color: transparent !important;
        border-bottom: 2px solid white !important;
        padding: 0 calc(1em + 5px);
        padding-bottom: 0.5em;

        .dateIncludedField {
          display: flex;
          flex-direction: column;
        }

        .dateLabel {
          color: rgb(175, 175, 175);
          font-size: 12px;
        }

        .accordionData {
          padding: 0.5em 0;
        }

        .dropdown {
          justify-content: space-between;
          gap: 5px;
        }

        [class~='MuiAccordionSummary-content'] {
          flex-grow: 0;
        }

        [class~='MuiAccordionSummary-root'] {
          min-height: auto;
        }

        [class~='MuiCollapse-root'] {
          padding: 0 5px;
          color: black;
        }

        [class~='Mui-expanded'] {
          min-height: auto;
        }

        .dataValue {
          font-size: 14px;
          color: black;
        }

        .dataLabel {
          font-size: 14px;
          color: rgb(175, 175, 175);
        }
      }

      .notesButtonWrap {
        display: flex;
        justify-content: space-between;
      }

      // desktop-first approach since platform is developed for desktop and tablets
      .open {
        opacity: 1;
        width: calc(33vw - 125px);
        @media (max-width: 1000px) {
          width: 100%;
          height: 70vh;
        }
      }

      .detailBtn {
        width: 3em;
        color: $color-fun-blue;
        background-color: #f2f6ff;
        // border-right: 3px $color-fun-blue solid;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        transition: all 0.2s ease;
        gap: 3em;
        @media (max-width: 1000px) {
          display: flex;
          flex-direction: row;
          width: 100%;
          border-right: none;
          border-bottom: 3px $color-fun-blue solid;
          gap: 1em;
          height: fit-content;
          svg {
            width: 50px;
            height: 50px;
          }
        }

        .baseIcon {
          path {
            fill: $color-fun-blue;
          }
        }

        .contractIcon {
          path {
            fill: $color-gray;
          }
        }

        .absenceIcon {
          path {
            fill: rgb(110, 160, 81);
          }
        }

        &:hover {
          background-color: color.scale($color-fun-blue, $lightness: 25%);
          color: $color-white;

          .baseIcon {
            path {
              fill: #fff;
              transition: all 0.2s ease;
            }
          }

          .contractIcon {
            path {
              fill: #fff;
              transition: all 0.2s ease;
            }
          }

          .absenceIcon {
            path {
              fill: #fff;
              transition: all 0.2s ease;
            }
          }
        }

        &.toggleBase {
          background-color: color.scale($color-fun-blue, $lightness: 25%);
          color: $color-white;

          &:hover {
            background-color: $color-fun-blue;
          }
        }

        &.toggleContract {
          background-color: color.scale($color-gray, $lightness: 25%);
          color: $color-white;

          &:hover {
            background-color: $color-gray;
          }
        }

        &.toggleAbsence {
          background-color: color.scale(rgb(110, 160, 81), $lightness: 25%);
          color: $color-white;

          &:hover {
            background-color: rgb(110, 160, 81);
          }
        }

        .sideTitle {
          font-size: rem(20px);
          font-weight: 600;
          transform-origin: right;
          transform: translateX(-50%) translateY(-50%) rotate(-90deg);
          @media (max-width: 1000px) {
            transform: none;
          }
        }
      }

      .contractBtn {
        color: $color-gray;
        background-color: $color-wild-sand;
        // border-right: 3px $color-gray solid;
        @media (max-width: 1000px) {
          border-right: none;
          border-bottom: 3px $color-gray solid;
        }

        &:hover {
          background-color: color.scale($color-gray, $lightness: 25%);
          color: $color-white;
        }
      }

      .absenceBtn {
        color: rgb(110, 160, 81);
        background-color: rgb(236, 253, 226);
        // border-right: 3px rgb(110, 160, 81) solid;
        @media (max-width: 1000px) {
          border-right: none;
          border-bottom: 3px rgb(110, 160, 81) solid;
        }

        &:hover {
          background-color: color.scale(rgb(110, 160, 81), $lightness: 25%);
          color: $color-white;
        }
      }
    }
  }
}

  .active {
    border-color: $color-black;
    background-color: $color-black-05;
  }
.dataLabelAttachment {
  padding: 10px 0;
  color:#3855b3;
}