@use 'assets/styles/shared' as *;

.absenceHeading {
  color: $color-fun-blue;
  padding: 0.5em 1em;
}

.absenceWrap {
  .absenceView {
    max-height: 430px;
    background-color: $color-wild-sand;
    overflow-y: scroll;

    .absenceList {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      gap: 5px;
      padding: 0.5em 0.5em;

      .content {
        display: flex;
        flex-direction: column;
      }
      .dataLabel {
        color: $color-gray;
        font-size: rem(12px);
      }
    }
    .noAbsence {
      padding: .5em .5em;
      text-align: center;
    }
  }
}
