@use 'assets/styles/shared' as *;

.swiperContainer {
  position: relative;
  flex: 1;
  margin-top: rem(14px);
  margin-bottom: rem(16px);

  .swiper {
    width: 100%;
    height: 100%;
    padding-bottom: rem(46px);
  }

  .arrowsContainer {
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: 0;

    .arrowButton {
      width: rem(34px);
      height: rem(34px);
      margin-left: rem(4px);
      border-radius: $radius-small;
      background: $color-fun-blue;
    }
  }
}
