@use 'assets/styles/shared' as *; 

.modal {
  max-height: calc(100vh - 112px);
  display: flex;
  gap: rem(12px);

  .articles {
    display: flex;
    flex-direction: column;
    flex: 0 0 rem(287px);

    h2 {
      color: $color-fun-blue;
      font-size: rem(18px);
      line-height: 1.5;
      font-weight: 600;
      margin: rem(20px) 0 rem(24px);
    }

    .items {
      display: flex;
      flex-direction: column;
      gap: rem(32px);
      padding-right: rem(8px);
      margin-bottom: rem(10px);
      overflow-y: auto;

      .articleBlock {
        margin: 0;
        width: 100%;
      }
    }

    .okButton {
      align-self: flex-end;
    }
  }

  .openedArticleWrapper {
    overflow-y: auto;
    flex-grow: 1;
    padding-right: rem(16px);

    .openedArticle {
      margin: 0;
      min-height: 100%;
    }
  }
}
