@use 'assets/styles/shared' as *;

.textSlider {
  .rc-slider {
    width: rem(30px);
    margin-left: rem(6px);
    margin-right: rem(2px);
    margin-top: rem(3px);
    cursor: pointer;

    &-rail,
    &-track {
      height: rem(2px);
      width: rem(30px);
      background: $color-alto;
    }
    &-handle {
      @include circle(6px);
      background: $color-gray;
      border: none;
      margin-top: rem(-2px);
      cursor: pointer;

      &:active {
        border-color: $color-silver;
        box-shadow: 0 0 5px $color-silver;
      }
    }
  }
}
