@use 'assets/styles/shared' as *;
@use "sass:color";

.calendar {
  display: flex;
  flex-direction: column;
  //justify-content: space-between;
  justify-content: center;
  height: 100%;
  margin: auto 0 0 rem(50px);
  .logo {
    width: 130px;
  }
  .blueLine {
    width: 100%; /* Adjust the width as needed */
    height: 1px; /* Adjust the height as needed */
    background-color: blue;
    margin-top: 50px; /* Adjust the margin as needed */
  }
  .item {
    display: flex;
    margin-bottom: 50px;

    img {
      width: 30px;
      height: 30px;
      margin-right: 10px;
    }
  }
  /********Temporary not use ********/
  .tasksWrapper {
    .tasksTitleWrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: rem(24px) rem(36px) rem(24px) rem(32px);

      .title {
        font-weight: 600;
        font-size: rem(18px);
        line-height: rem(22px);
        letter-spacing: -0.4px;
        color: $color-woodsmoke-90;
      }

      .viewAll {
        font-weight: 600;
        font-size: rem(12px);
        line-height: number(12, 18);
        letter-spacing: -0.3px;
        color: $color-azure;

        &:hover {
          color: color.scale($color-fun-blue, $lightness: 25%);
        }
      }
    }
    .taskList {
      margin: 0 rem(36px);

      .checkbox {
        margin-bottom: rem(24px);

        input:checked + label {
          text-decoration: line-through;
          font-size: rem(12px);
          line-height: rem(18px);
          color: $color-woodsmoke-42;
        }

        .text {
          margin-top: -3px;
          margin-left: rem(16px);
        }

        .ticketNumber {
          color: $color-azure;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }

  .addTaskWrapper {
    display: flex;
    align-items: center;
    margin: rem(38px) rem(30px) rem(82px) rem(36px);
    padding: rem(16px);
    padding-right: rem(12px);
    background: $color-white;
    border: 1px dashed $color-iron-dark;
    border-radius: 8px;

    &:hover {
      background: color.scale($color-white, $lightness: -5%);
    }

    svg {
      margin-right: rem(18px);
    }
  }
}
