@use './assets/styles/shared.scss' as *;

body div {
  > .tippy-box {
    background-color: $color-white;
    box-shadow: 6px 6px 5px $color-black-05;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: normal;
    font-size: rem(14px);
    line-height: number(14, 19);
    letter-spacing: 0.01em;
    color: $color-jackson-blue;
    border-radius: 0;

    > .tippy-content {
      padding: 0;
    }
  }
}
