@use 'assets/styles/shared' as *;
@use 'sass:color';

.rowItem {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: rem(6px);
  color: $color-fun-blue;

  .clickableArea {
    display: flex;
    align-items: center;
    flex: 1;
    padding: rem(32px) rem(24px) rem(32px) rem(10px);
    height: 100%;

    @include media(1660px) {
      font-size: rem(12px);
      padding: rem(26px) 0 rem(26px) rem(6px);
    }
    @include media(1360px) {
      font-size: rem(10px);
      padding: 0;
      padding-left: rem(6px);
    }
  }

  &.gray {
    background-color: $color-gallery;
    width: 14%;

    &:hover,
    &.searched {
      background-color: color.scale($color-gallery, $lightness: -15%);
    }
  }
  &.blue {
    background-color: $color-white-lilac;
    width: 25%;

    .clickableArea:hover,
    &.searched .clickableArea {
      background-color: $color-fun-blue;
      color: $color-white;

      + .menuIcon {
        rect {
          fill: $color-white;
        }
      }
    }

    .number {
      background-color: $color-pigeon-post;
    }
  }
  &.orange {
    background-color: $color-old-lace;
    color: $color-clementine;
    width: 16.2%;

    .clickableArea {
      justify-content: center;

      @include media(1360px) {
        padding: 0;
      }
    }

    &:hover,
    &.searched {
      background-color: $color-clementine;
      color: $color-white;
    }
  }

  .number {
    @include circle(24px);
    @include f-center;
    margin-right: rem(8px);
    background-color: $color-fun-blue;
    color: $color-white;
    flex-shrink: 0;

    @include media(1660px) {
      @include circle(20px);
      margin-right: rem(5px);
    }

    @include media(1360px) {
      @include circle(16px);
    }
  }

  .text {
    white-space: pre;
  }

  .menuIcon {
    position: absolute;
    right: rem(6px);
    top: rem(6px);
  }

  .menuIcon:hover {
    rect {
      fill: $color-white;
    }
  }
}

@keyframes highlight {
  0% {
    color: $color-fun-blue;
  }
  25% {
    color: $color-clementine;
  }
  75% {
    color: $color-clementine;
  }
  100% {
    color: $color-fun-blue;
  }
}

.menu {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  margin-top: rem(10px);
  margin-right: rem(30px);
  margin-left: rem(10px);
  background: $color-wild-sand;
  border-top-right-radius: rem(6px);
  min-width: rem(172px);
  max-width: rem(275px);
  min-height: rem(100px);

  @include media(1360px) {
    max-width: rem(230px);
  }

  .cancel {
    position: absolute;
    top: rem(8px);
    right: rem(14px);

    rect {
      fill: $color-fun-blue;
    }
  }

  .menuItemsList {
    margin-top: rem(26px);
    margin-left: rem(20px);
    display: flex;
    flex-wrap: wrap;

    .menuItem {
      font-size: rem(12px);
      line-height: number(12, 16);
      color: $color-fun-blue;
      display: flex;
      margin-right: rem(24px);
      margin-bottom: rem(12px);
      text-align: left;
      transition: color 0.5s;

      &.searched {
        animation: highlight 1.5s 2;
      }

      @include media(1360px) {
        font-size: rem(10px);
      }

      .text {
        display: flex;
      }

      .arrow {
        margin-left: rem(12px);
      }
    }
  }
}
