@use 'assets/styles/shared' as *;

.bigMenu {
  display: flex;
  flex-direction: column;
  align-items: center;

  .close {
    align-self: flex-end;
    margin-top: rem(38px);
    margin-right: rem(36px);
  }

  .logo {
    width: rem(176px);
    height: rem(60px);
    margin-top: rem(14px);
  }

  .menuItemsWrapper {
    height: 65vh;
    margin-top: rem(52px);
    @include f-column;
    justify-content: center;
    overflow: auto;
  }
}
