@use 'assets/styles/shared' as *;

.widget {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: $color-white;
  border: 2px solid $color-wild-sand;
  box-sizing: border-box;
  border-radius: $radius-medium;
  overflow-y: auto;

  .header {
    @include f-center-between;
    background: $color-wild-sand;
    padding: rem(12px) rem(32px);
    color: $color-fun-blue;
    position: sticky;
    top: 0;
    z-index: 10;

    .title {
      font-weight: 600;
      font-size: rem(18px);
      line-height: number(18, 26);
    }
  }

  .body {
    display: flex;
    flex: 1;
    flex-direction: column;
    margin-left: rem(32px);
    margin-right: rem(32px);
  }
}
