@use 'assets/styles/shared' as *;
@use "sass:color";

.bigMenu {
  display: flex;
  flex-direction: column;
  height: 90%;

  .tabs {
    @include f-center;
    margin-top: rem(24px);
    text-align: center;
    color: $color-white;

    .tab {
      font-size: rem(12px);
      line-height: number(12, 16);
      padding: rem(4px) rem(32px);
      background: $color-fun-blue-60;
      height: rem(22px);

      &:hover {
        background: color.scale($color-fun-blue-60, $lightness: 25%);
      }

      &.selected {
        background: $color-clementine;

        &:hover {
          background: color.scale($color-clementine, $lightness: -15%);
        }
      }

      &:first-child {
        border-radius: 7px 0px 0px 7px;
        border-right: 1px solid $color-white;
      }

      &:last-child {
        border-radius: 0px 7px 7px 0px;
      }
    }
  }
}
