@use 'assets/styles/shared' as *;

.navigation {
  margin: rem(15px) rem(35px) 0;
  padding: rem(5px) rem(15px);
  background-color: $color-wild-sand;
  position: relative;
  z-index: 1;

  .navigationItem {
    background: none;
    color: $color-fun-blue;
    cursor: pointer;
    font-size: rem(12px);

    &.selected {
      color: $color-clementine;
      text-decoration: underline;
    }

    + .navigationItem {
      margin-left: 30px;
    }
  }
}
