@use 'assets/styles/shared' as *;

.commentsContainer {
  margin-bottom: rem(24px);
  background: $color-wild-sand;
  border: 1px solid $color-alto-lighter;

  &.reply {
    border: none;
    border-left: 2px solid $color-alto-lighter;
    margin-left: rem(8px);
  }

  .commentsHeader {
    display: flex;
    justify-content: space-between;

    .userTitle {
      display: flex;
      align-items: flex-end;

      .avatar {
        @include circle(38px);
        @include f-center;
        background: $color-white;
        overflow: hidden;
        margin: rem(6px);
      }

      .textContainer {
        display: flex;
        align-items: center;
        align-self: flex-end;
        margin-bottom: rem(6px);
        font-weight: 600;
        font-size: rem(12px);
        line-height: rem(16px);
        color: $color-fun-blue;

        .userName {
          margin-right: rem(12px);
        }

        .dot {
          @include circle(2px);
          background: $color-jackson-blue;
          margin-right: rem(4px);
        }
      }
    }

    .arrowContainer {
      transform: rotate(0deg);
      transition: transform 0.3s;
      align-self: center;
      margin-right: 16px;

      &.expanded {
        transform: rotate(90deg);
      }
    }
  }

  .commentBody {
    margin: rem(8px);
    font-weight: 400;
    font-size: rem(12px);
    line-height: rem(16px);
    color: $color-black;

    .commentTitle {
      font-weight: 600;
    }
  }

  .commentsFooter {
    display: flex;
    justify-content: flex-end;
    margin-bottom: rem(8px);
    margin-top: rem(14px);
    margin-right: rem(14px);

    .iconWrapper {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: rem(10px);
      line-height: number(10, 12);
      color: $color-black;

      svg {
        margin-top: rem(2px);
        margin-right: rem(2px);
      }
    }

    .editWrapper {
      @extend .iconWrapper;

      &.active {
        color: $color-clementine;

        path {
          fill: $color-clementine;
        }
      }
    }

    .chatIconWrapper {
      @extend .iconWrapper;
      margin-left: rem(26px);

      svg {
        path:first-child {
          fill: $color-alto-lighter;
        }
      }
    }
  }
}
