@use 'assets/styles/shared' as *;
@use "sass:color";

.openArticleContainer {
  @include f-center-start;
  color: $color-fun-blue;

  &:hover {
    color: color.scale($color-fun-blue, $lightness: 25%);
  }

  &:active {
    color: color.scale($color-fun-blue, $lightness: -15%);
  }
}

.linkText {
  font-weight: 600;
  font-size: rem(12px);
  line-height: number(12, 16);
  margin-right: rem(16px);
}
