@use 'assets/styles/shared' as *;

.footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 rem(12px) rem(32px);
  gap: rem(10px)
}

.container {
  padding: rem(32px) rem(16px) rem(6px) rem(16px);
  background: $color-wild-sand;
  border: 2px solid $color-wild-sand;
  box-sizing: border-box;
  border-radius: 8px;
  overflow: auto;
  max-height: calc(100vh - 200px);
}
