@use "sass:selector";
@use 'assets/styles/shared' as *;

.button {
  margin-left: auto;
  margin-bottom: 15px;
  font-weight: 700;
}

.fieldTitle {
  font-size: 12px;
  margin-bottom: 10px;
  margin-top: 15px;
  color: $color-fun-blue;
}

.removeButton {
  width: 20px;
  font-size: 25px;
  margin-bottom: 5px;
  margin-left: auto;
}

.fieldWrap {
  display: flex;
  justify-content: space-between;
  align-items: center;

  >:first-child {
    width: 100%;
    flex-basis: auto;
  }



  &.group {
    position: relative;
    background-color: #fafafa;
    padding: 30px 20px 10px;
    border-radius: 7px;

    .removeButton {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}

.addFirstEntry {
  margin: rem(20px) auto;
}

.dropdown {
  background-color: #f5f5f5 !important;
}

.warningModal {
  padding: 10px 10px 0 10px;

  .modalHeader {
    font-weight: 600;
    margin-bottom: 10px;
    color: $color-fun-blue;
  }

  .warningText {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  .warningTitle {
    font-size: 14px;
    color: #979797;
  }

  .buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
}