@use 'assets/styles/shared' as *;
@use 'sass:color';

.title {
  @include pageTitle;
}

.zoomableContainer {
  overflow: hidden;
  height: calc(100vh - 205px);
  display: flex;
  width: 100%;
  align-items: center;

  .zoomable {
    width: 100%;
    position: relative;
    transition: transform 0.5s, left 0.5s, top 0.5s;

    &.zoomed {
      transform: scale(3);
      transition: transform 0.5s, left 0.5s, top 0.5s;

      // @include media(1660px) {
      //   transform: scale(3);
      // }
    }
  }
}

.section {
  margin: rem(18px) 0 rem(70px) rem(34px);
  display: flex;

  .sectionTitle {
    color: $color-white;
    font-size: rem(18px);
    font-weight: 600;
    width: rem(40px);
    margin-right: rem(14px);

    @include media(1660px) {
      font-size: rem(16px);
      width: rem(35px);
      margin-right: rem(12px);
    }
    @include media(1360px) {
      font-size: rem(14px);
      width: rem(32px);
      margin-right: rem(10px);
    }

    &.blue {
      background-color: $color-fun-blue;
    }

    &.orange {
      background-color: $color-clementine;

      div {
        margin-top: rem(225px);
      }
    }

    div {
      transform: rotate(-90deg);
      white-space: nowrap;
      margin-top: rem(275px);

      @include media(1660px) {
        margin-top: rem(210px);
      }
      @include media(1360px) {
        margin-top: rem(182px);
      }
    }
  }

  .rows {
    width: 100%;

    .row {
      display: flex;
      margin-bottom: rem(8px);
      height: rem(120px);

      @include media(1660px) {
        height: rem(90px);
      }
      @include media(1360px) {
        height: rem(80px);
      }
      @include media(1100px) {
        height: rem(72px);
      }

      &:last-child {
        margin-bottom: 0;
      }

      &.orange {
        height: rem(80px);
      }

      .rowTitle {
        color: $color-fun-blue;
        margin-right: rem(12px);
        width: 12.5%;
        padding: rem(14px);

        @include media(1660px) {
          font-size: rem(12px);
        }
        @include media(1360px) {
          font-size: rem(10px);
        }
        @include media(1100px) {
          padding: rem(10px);
        }

        div {
          font-weight: 700;

          &.number {
            font-size: rem(20px);
            font-weight: bold;

            @include media(1660px) {
              font-size: rem(16px);
            }
            @include media(1360px) {
              font-size: rem(14px);
            }
          }
        }

        &.gray {
          background-color: $color-alto;
        }
        &.blue {
          background-color: $color-periwinkle-gray;
        }
        &.orange {
          background-color: $color-tuft-bush;
          display: flex;
          align-items: center;
          color: $color-clementine;

          @include media(1420px) {
            padding-left: rem(4px);
            padding-right: rem(4px);
          }
        }
      }
      .rowItemsWrapper {
        display: flex;
        width: 87%;
        margin-right: 20px;
      }
    }
  }
}
