@use 'assets/styles/shared' as *;

.title {
  @include pageTitle;
}

.loader {
  margin-top: rem(20px);
}

.content {
  padding: rem(30px);

  .editor {
    border: none;
    width: 75%;
    height: calc(100vh - #{rem(75px)});

    @media screen and (min-width: 3840px) {
      width: 65%;
    }
    
    @media screen and (min-width: 2560px) {
      width: 70%;
    }
  
    @media screen and (max-width: 1400px) {
      width: 100%;
    }
  
    @media screen and (max-width: 1024px) {
      width: 100%;
    }
  
    @media screen and (max-width: 768px) {
      width: 100%;
    }
  }

  h2 {
    margin-bottom: rem(8px);
    font-weight: 600;
    color: $color-fun-blue;
  }
}

.editButtonRow {
  display: flex;
  justify-content: flex-end;
  margin-bottom: rem(20px);
}

.accordion {
  .summary {
    border-bottom: 1px solid $color-silver;
    color: $color-jackson-blue;

    :global(.MuiAccordionSummary-expandIconWrapper) {
      order: -1;
      margin-right: rem(16px);
      transform: rotate(0deg) !important;

      &:global(.Mui-expanded) {
        transform: rotate(45deg) !important;
      }

      svg {
        fill: $color-jackson-blue;
      }
    }
  }

  .content {
    padding-left: rem(30px);
  }
}

.detailsPage {
  overflow-x: hidden;
  width: 75%;

  @media screen and (min-width: 3840px) {
    width: 65%;
  }
  
  @media screen and (min-width: 2560px) {
    width: 70%;
  }

  @media screen and (max-width: 1400px) {
    width: 100%;
  }

  @media screen and (max-width: 1024px) {
    width: 100%;
  }

  @media screen and (max-width: 768px) {
    width: 100%;
  }

  h1 {
    font-size: rem(24px);
    line-height: number(24, 33);
    letter-spacing: 0.01em;
    color: $color-fun-blue;
    margin-bottom: rem(7px);
  }

  .text {
    color: $color-jackson-blue;
    text-align: center;
    margin-bottom: rem(30px);
  }

  h2,
  h3,
  h4 {
    color: $color-fun-blue;
    font-weight: 600;
    margin-bottom: rem(12px);
  }

  h4 {
    font-weight: 700;
  }

  a {
    color: $color-fun-blue;

    &:active,
    &:focus,
    &:hover {
      color: $color-clementine;
      text-decoration: underline;
    }
  }

  table {
    border: 1px solid $color-wild-sand;
    border-spacing: 0;

    tr {
      td,
      th {
        border: 1px solid transparent;

        &:not(:last-child) {
          border-right-color: #bbb;
        }
      }

      &:hover {
        td,
        th {
          border-top-color: $color-clementine;
          border-bottom-color: $color-clementine;

          &:first-child {
            border-left-color: $color-clementine;
          }

          &:last-child {
            border-right-color: $color-clementine;
          }
        }
      }
    }

    tbody {
      tr:nth-child(2n + 1) {
        background-color: $color-wild-sand;
      }

      tr:nth-child(2n) {
        background-color: $color-white;
      }
    }

    th,
    td {
      padding: 8px 6px;
    }

    th {
      background-color: $color-white;
      color: $color-jackson-blue;
      font-weight: 600;
    }
  }

  * {
    max-width: 100% !important;
  }
}

.dndContainer {
  height: rem(150px);
  border: 1px solid rgba(208, 208, 208, 0.9);
  border-radius: 4px;
  background: #fff;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0.3;
    z-index: -1;
  }

  &.active {
    border-color: $color-black;
    background-color: $color-black-05;
  }

  .upload {
    padding-top: 5px;
    padding-bottom: 5px;
  }
}
