@use 'assets/styles/shared' as *;

.labelContainer {
  label {
    position: absolute;
    left: 0;
    top: 0;
    max-width: calc(100% - 24px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    font-size: 1rem;
    line-height: 1.4375;
    color: rgba(0, 0, 0, 0.6);

    transform: translate(14px, 9px) scale(1);
    transform-origin: top left;
    transition-property: color, transform;
    transition-duration: 200ms;
    transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
    z-index: 1;
    pointer-events: none;
  }

  fieldset {
    position: absolute;
    bottom: 0;
    right: 0;
    top: -5px;
    left: 0;
    margin: 0;
    padding: 0 8px;

    border-radius: 4px;
    border: 1px solid rgba(208, 208, 208, 0.9);
    pointer-events: none;

    legend {
      font-size: 0.75em;
      visibility: hidden;
      height: 11px;
      padding: 0;
      width: 0;
    }
  }

  &.disabled {
    label {
      color: rgba(0, 0, 0, 0.38);
    }

    fieldset {
      border-color: $color-black;
    }
  }

  &.filled {
    label {
      transform: translate(14px, -9px) scale(0.75);
    }

    fieldset {
      border-color: $color-black;

      legend {
        width: auto;
        padding: 0 5px;
      }
    }
  }

  &.focused {
    label {
      display: none;
    }

    fieldset {
      border-color: $color-clementine;

      legend {
        visibility: initial;
        color: $color-clementine;
        width: auto;
        padding: 0 8px;
      }
    }
  }

  &.selected {
    label {
      display: none;
    }
    fieldset{
      top: -2px;
      legend{
        padding: 0;
        display: none;
      }
    }
  }
}

:global(.react-select-container):hover {
  fieldset {
    border-color: $color-clementine;
  }
}
