@use 'assets/styles/shared' as *;

.newsItem {
  margin-top: rem(18px);
  margin-bottom: rem(24px);

  .scrollable {
    max-height: 26vh;
    overflow-y: auto;
    overflow-x: hidden;
    margin-bottom: rem(22px);
  }

  .imageContainer {
    margin-bottom: rem(8px);

    img {
      max-height: rem(95px);
      width: auto;
    }
  }

  [class*='bodyText'] {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
  }
}

.news {
  width: 100%;
  aspect-ratio: 16 / 9;
  background-color: #e5e5e5;
  background-size: cover;
  color: #fff;
  position: relative;
  display: flex;
  border-radius: 7px;
  flex-direction: column;
  overflow: hidden;
  font-size: .75rem;
  
  &::before {
    content: "";
    position: absolute;
    display: block;
    background:linear-gradient(0deg,rgba(0,0,0,.6) 30%, transparent);
    width: 100%;
    height: 50%;
    bottom: 0;
    left: 0;
    z-index: 0;

  }

  .animated {
    margin-top: auto;
    border-radius: 7px;
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    transition: all 1s;
    height: 70px;
    z-index: 1;

    .newsTitle {
      color: #fff;
    }

    .plusButton {
      width: 30px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      background: $color-gray;
      border-radius: 50%;
    }
    &.orange .plusButton {
      background-color: $color-clementine;
    }
    &.green .plusButton {
      background-color: $color-green;
    }
    &.blue .plusButton {
      background-color: $color-jackson-blue;
    }

    > *:not(.plusButton, .newsTitle, .categoryLabel) {
      display: none;
    }
  }

  .bottomControls {
    margin-top: auto;

    .more {
      font-size: .875rem;
      align-items: center;
      font-weight: 700;
      text-align: left;
    }
  }

  &:hover {
    .animated {
      animation: height 1s 1;
      height: 200px;
      background-color: transparentize($color-dark, 0.2);
      
      &.orange {
        background-color: transparentize($color-clementine, 0.2);

        .more svg path {
          fill: #fff;
        }
      }
      &.green {
        background-color: transparentize($color-green, 0.2);
      }
      &.blue {
        background-color: transparentize($color-jackson-blue, 0.2);
      }
      .date, .sub {
        display: block
      }
      .bottomControls, .more {
        display: flex;
      }
      .description {
        display: -webkit-box;
      }
      .plusButton {
        display: none;
      }
    }
    .categoryLabel {
      display: none;
    }
  }
  .bigItemContainer{
    margin-left: rem(26px);
    position: sticky;
    top: 0;
    overflow: hidden;
  
    .cancel {
      position: absolute;
      z-index: 2;
      right: -3px;
      top: 0;
    }
  }
  
  .itemBlock {
    width: 100%;
    margin: 0;
  }
}