@use 'assets/styles/shared' as *;

.container {
  background: #f5f5f5;
  border-radius: 7px;
  margin-left: rem(12px);
  margin-right: rem(30px);
  padding-bottom: rem(4px);

  .documentContainer {
    height: 65vh;
    overflow-y: auto;
    .content {
      margin: 0 auto;
      width: rem(595px);
      height: inherit;
    }
    .page {
      margin-bottom: rem(3px);
    }
  }
  .buttonsContainer {
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    margin: rem(4px);
    padding: rem(8px);

    .button {
      @include blueButton;
    }

    .buttonPrimary {
      @include orangeButton;
      margin-left: rem(16px);
    }
  }
}
