@use 'assets/styles/shared' as *;
@use "sass:color";

.button {
  @include blueButton;
}

.controls {
  margin-left: rem(35px);
  display: flex;
  justify-content: space-between;

  .navigation {
    display: flex;
    margin-top: rem(10px);
  }
}

.searchAndTableWrapper {
  width: fit-content;
  padding-right: rem(30px);
  margin-left: rem(35px);

  .search {
    margin: rem(34px) 0 rem(10px) 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: rem(30px) rem(30px) 0;

  .title {
    @include pageTitle;
    margin: 0;
  }
}
