@use 'assets/styles/shared' as *;

.bigMenuItem {
  opacity: 0.7;
  display: flex;
  margin-bottom: rem(40px);
  margin-left: rem(30px);

  &:hover {
    opacity: 1;
  }

  &.selected {
    opacity: 1;
  }

  &.withItems {
    flex-direction: column;
  }

  .clickableWrapper {
    display: flex;
    align-items: center;
  }

  .name {
    font-size: rem(14px);
    line-height: 20px;
    letter-spacing: -0.3px;
    color: $color-white;
    margin-left: rem(12px);
  }

  .subitemsContainer {
    margin-left: rem(36px);
    margin-top: rem(6px);
  }

  .iconWrapper {
    @include f-center;
  }
}
