@use 'assets/styles/shared' as *;

.loader {
  @include circle(5rem);
  margin: auto;
  border: 3px solid $color-fun-blue;
  border-left-color: transparent;
  border-right-color: transparent;
  animation: spin 2s ease infinite;

  &.small {
    width: 3rem;
    height: 3rem;
    border-width: 2px;
  }
}
