@use 'assets/styles/shared' as *;

.memberInfo {
  padding: 20px;
  font-size: rem(14px);
}
.header {
  color: $color-fun-blue;
  font-size: rem(12px);
  h3 {
    font-weight: 600;
    font-size: rem(18px);
    line-height: rem(25px);
  }
}
.photo {
  max-width: rem(110px);
  width: auto;
  margin: 8px 0;
}
.label {
  display: block;
  color: $color-dusty-gray;
  font-size: rem(12px);
}
.section {
  margin-bottom: 15px;
}
.group {
  padding: 8px 16px;
  border-radius: 4px;
  width: fit-content;
  color: $color-fun-blue;
  margin-top: 10px;
  display: flex;
  align-items: center;

  svg {
    margin-left: 15px;
  }
}

.loader {
  margin-top: rem(20px);
}
.link{
  color: $color-fun-blue;
  &:hover{
    text-decoration: underline;
  }
}