@use 'assets/styles/shared' as *;

[class*='MuiList-root'] {
  background-color: $color-wild-sand;
}

[class*='MuiPaper-root'] {
  border-radius: 8px !important;
}

.dropdownItem {
  color: $color-fun-blue !important;
  font-size: rem(12px) !important;
  font-weight: 600 !important;
  font-family: "Open Sans", sans-serif !important;
  transition: color 200ms;

  &:hover {
    color: $color-clementine !important;
  }
}

.navigation {
  z-index: 1;

  .dropdownLabel {
    color: $color-fun-blue;
    font-size: rem(12px);
    font-weight: 600;
    font-family: "Open Sans", sans-serif;
    padding: 9px 14px;
    letter-spacing: normal;
  }

  .dropdownContainer {
    background-color: $color-wild-sand;
    border-radius: 8px;
    color: $color-fun-blue;
    font-size: rem(12px);
    font-weight: 600;
    height: 3em;
    width: 200px;
  }

  .navigationItem {
    background: none;
    color: $color-fun-blue;
    cursor: pointer;
    font-size: rem(12px);

    &.selected {
      color: $color-clementine;
      text-decoration: underline;
    }

    + .navigationItem {
      margin-left: 30px;
    }
  }
}