@use 'assets/styles/shared' as *;

.container {
  width: fit-content;
  max-width: 100%;

  .tableContainer {
    border: 1px solid $color-wild-sand;

    .table {
      display: inline-block;
      max-width: 100%;

      :first-child {
        max-width: 100%;
      }

      .th,
      .td {
        /* In this example we use an absolutely position resizer,
           so this is required. */
        position: relative;
        padding: rem(8px) rem(6px);

        :last-child {
          border-right: 0;
        }

        .resizer {
          display: inline-block;
          background: $color-wild-sand;
          width: 6px;
          height: 100%;
          position: absolute;
          right: 0;
          top: 0;
          transform: translateX(50%);
          z-index: 1;
          /* prevents from scrolling while dragging on touch devices */
          touch-action: none;

          &.isResizing {
            background: $color-wild-sand;
          }
        }
      }

      .th {
        border: 1px solid $color-white;
      }

      .tbody {
        .tr {
          border: 1px solid transparent;
          position:relative!important;
          display: flex!important;
          width: 100%!important;
          top: 0!important;
          height: 32px!important;
          &:hover {
            cursor: pointer;
            border-color: $color-clementine;
          }

          &:nth-child(even) {
            background-color: $color-wild-sand;
          }

          &.selected {
            border: 1px solid $color-clementine;
          }
          &.expanded {
            height: 110px!important;
          }
        }
      }

      .td {
        font-size: rem(12px);
        line-height: number(12, 16);
        color: $color-black;
        border-right: 2px solid $color-white;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
}
