@use 'assets/styles/shared' as *;
@use "sass:color";

.rightMenuWrap {
  position: relative;
}

.rightMenu {
  position: fixed;
  right: 0;
  z-index: 100;
  height: 100vh;
  width: $right-menu-width;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: $color-athens-gray;
  box-shadow: $shadow-blur2-10-iron;
  transition: width 300ms;

  .iconWrapper {
    @include f-center;
    position: relative;
  }

  .iconGroup {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    margin: rem(26px);
    position: relative;
    transition: transform 150ms ease-in-out;

    .bellIcon {
      @extend .iconWrapper;

      &:hover {
        path {
          fill-opacity: 1;
        }
      }
    }

    .mailIcon {
      @extend .iconWrapper;
      // margin-top: rem(28px);

      &:hover {
        path {
          opacity: 0.7;
        }
      }
    }

    .bellNotification {
      @include circle(5px);
      background-color: $color-clementine;
      position: absolute;
      right: -3px;
      top: -2px;
    }

    .mailNotification {
      @include circle(18px);
      @include f-center;
      background: $color-clementine;

      font-family: $font-primary;
      font-weight: 500;
      font-size: rem(12px);
      line-height: number(12, 18);
      text-align: center;
      letter-spacing: -0.3px;
      color: $color-white;

      position: absolute;
      top: -12px;
      right: -12px;
    }

    .divider {
      height: 1px;
      width: rem(42px);
      background-color: $color-alto;
      align-self: center;
      margin-top: rem(10px);
      margin-bottom: rem(18px);
      position: absolute;
      top: rem(20px);
    }
  }

  &.big {
    width: rem(340px);
    display: block;

    .iconGroup {
      height: rem(24px);
    }
  }

  .expandButton {
    @include circle(38px);
    @include f-center;
    background: $color-fun-blue;
    margin-left: rem(-20px);
    position: absolute;
    top: calc(50% - 19px);
    z-index: 1;

    &:hover {
      background: color.scale($color-fun-blue, $lightness: 25%);
    }
  }

  .animatedButton {
    @include circle(38px);
    color: $color-white;
    background: $color-fun-blue;
    border: 0;
    position: absolute;
    margin-left: rem(-20px);
    top: calc(50% - 19px);
    z-index: 1;

    &:hover {
      background: color.scale($color-fun-blue, $lightness: 25%);
    }

    &.toggled {
      background: $color-clementine;

      &:hover {
        background: color.scale($color-clementine, $lightness: -15%);
      }

      & span {
        transform: rotate(90deg);
        &:last-child {
          left: 50%;
          right: 50%;
        }
      }
    }

    & span {
      position: absolute;
      transition: 800ms;
      background: white;
      border-radius: 5px;

      &:first-child {
        top: 28%;
        bottom: 28%;
        width: 2px;
        left: calc(50% - 1px);
      }

      &:last-child {
        left: 28%;
        right: 28%;
        height: 2px;
        top: calc(50% - 1px);
      }
    }
  }
}
