@use 'assets/styles/shared' as *;
@use "sass:color";

.formContainer {
  .formTitle {
    @include formTitle;
    margin-bottom: rem(10px);
    margin-top: rem(16px);
  }
  .form {
    padding: rem(18px) rem(16px);
    background: $color-wild-sand;
    border: 2px solid $color-wild-sand;
    box-sizing: border-box;
    border-radius: 8px;
    overflow: auto;
    max-height: calc(100vh - 200px);

    .input {
      margin-bottom: rem(14px);
    }
  }
  .formFooter {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: rem(16px) rem(12px) rem(12px);

    .footerButtons {
      display: flex;
      width: 100%;
      justify-content: flex-end;

      .button {
        @include blueButton;
      }

      .buttonPrimary {
        margin-left: 16px;
        svg {
          margin-left: 10px;
          path {
            fill: #fff;
          }
        }
      }
    }
  }
}
