@use 'assets/styles/shared' as *;

.menuSectionWrapper {
  @include f-column;
  @include f-center;
  width: 80%;
  margin: auto;
  font-weight: 600;
  font-size: rem(12px);
  line-height: rem(18px);
  text-align: center;
  letter-spacing: -0.3px;
  word-break: break-word;
  color: $color-white;
  opacity: 0.7;
  // margin-bottom: rem(40px);

  &:hover {
    opacity: 1;
  }

  &.selected {
    opacity: 1;
  }

  &.highlighted {
    font-weight: 600;
  }

  &.withDropdown {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: rem(12px);
      height: rem(30px);
      background: $color-wild-sand;
      right: -14px;
      top: 0;
    }
  }
}

.dropdownContent {
  padding: rem(20px) rem(26px) rem(10px);
  min-width: rem(300px);

  div {
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;

    > button {
      text-align: left;
    }
  }
  > div {
    border-bottom: 1px solid darken($color: $color-wild-sand, $amount: 1) ;
  }
}
