@use 'assets/styles/shared' as *;
@use "sass:color";

.calendar {
  .day {
    @include f-center;
    font-family: $font-primary;
    font-weight: 900;
    font-size: rem(24px);
    line-height: number(24, 32);
    text-align: center;
    letter-spacing: -0.3px;
    color: $color-fun-blue;
    background: $color-white;
    border-radius: $radius-medium;
    width: rem(34px);
    margin: auto;
  }

  .monthYear {
    font-family: $font-primary;
    font-size: rem(12px);
    line-height: number(12, 12);
    text-align: center;
    letter-spacing: -0.3px;
    color: $color-fun-blue;
    margin-bottom: rem(20px);
    margin-top: rem(4px);

    div:first-child {
      margin-bottom: rem(3px);
    }
  }
}
