@use 'assets/styles/shared' as *;
@use "sass:color";

.linkItem {
  display: flex;
  align-items: center;
  margin-right: rem(20px);

  .linkItemButton {
    font-weight: 600;
    font-size: rem(12px);
    line-height: number(12, 16);
    text-align: center;
    color: $color-fun-blue;
    margin-right: rem(8px);
    padding: 4px 0;
  }

  &:hover {
    .linkItemButton {
      color: color.scale($color-fun-blue, $lightness: 25%);
    }
    path {
      fill: color.scale($color-clementine, $lightness: -15%);
    }
  }

  &:active {
    .linkItemButton {
      color: $color-fun-blue;
    }
    path {
      fill: $color-clementine;
    }
  }
}
