@use 'assets/styles/shared' as *;

$blue: $color-fun-blue;
$lightBlue: #ECF0F2;
$gray: #4F4F4F;
$lightGray: #F5F5F5;
$green: #548235;
$lightGreen: #E2F0D9;
$orange: #EE5F00;
$lightOrange: #FFF5EF;
$yellow: $color-yellow;
$lightYellow: $lighter-yellow;

.row {
    display: flex;
    align-items: center;
    padding: 3px 10px;
    cursor: pointer;

    .groupTitle {
        display: none;
        font-weight: 600;
        font-size: rem(12px);
        line-height: number(12, 16);
        color: $color-fun-blue;
        width: 140px;
        overflow-x: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        flex-shrink: 0;

        &:hover {
            text-decoration: underline;
        }
    }
    .icon {
        flex-shrink: 0;

        &::before {
            content: url('../../../../../assets/icons/group.svg');
            display: inline-block;
            width: 25px;
            height: 25px;
            vertical-align: middle;
        }
        &:hover, &:active {
            &::before {
                content: url('../../../../../assets/icons/groupActive.svg');
            }
        }
    }
    &.active {
        .icon::before {
            content: url('../../../../../assets/icons/groupActive.svg');
        }
    }

    .imgWrap {
        width: 135px;
        height: 100%;
        opacity: 0.5;
        display: block;
        transition: opacity 3ms;
        pointer-events: all;
        //border-radius: 50%;
        overflow: hidden;

        &:hover, &.active {
            opacity: 1;
        }
        .defaultImg{
            border-radius: 50%;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }

    .lead {
        border-left: 2px solid #fff;
        border-right: 2px solid #fff;
        margin: 0 10px;
        padding: 0 0.5rem;
        flex-shrink: 0;
    }
    .members {
        display: flex;
        align-items: center;
        flex-grow: 1;
        overflow: hidden;

        .imgWrap {
            margin-right: 10px;
        }
    }
    .arrow {
        margin-left: auto;
    }
}

.detailRow{
    margin-top: -4px;
    .photo{
        margin-right: 10px;
        &:first-child{
            margin-left: 229px;
        }
    }
}

:global {
    .blue {
        :local(.row), &[class*="group"], &[class*="item"] {
            background-color: $lightBlue;
        }

        [class*="header"] {
            color: #fff;
            background-color: $blue;
        }
    }
    .green {
        :local(.row), &[class*="group"], &[class*="item"]{
            background-color: $lightGreen;
        }

        [class*="header"] {
            color: #fff;
            background-color: $green;
        }
    }
    .gray {
        :local(.row), &[class*="group"] {
            background-color: $lightGray;
        }

        [class*="header"] {
            color: #fff;
            background-color: $gray;
        }
    }
    .orange {
        :local(.row), &[class*="group"], &[class*="item"] {
            background-color: $lightOrange;
        }

        [class*="header"] {
            color: #fff;
            background-color: $orange;
        }
    }
    .yellow {
        :local(.row), &[class*="group"], &[class*="item"] {
            background-color: $lightYellow;
        }

        [class*="header"] {
            color: #fff;
            background-color: $yellow;
        }
    }


    [class*="projectGroups"] {
        &.blue [class*="header"] {
            background-color: $blue;
            color: #fff;
        }
        &.green [class*="header"] {
            background-color: $green;
            color: #fff;
        }
        &.gray [class*="header"] {
            background-color: $gray;
            color: #fff;
        }
        &.orange [class*="header"] {
            background-color: $orange;
            color: #fff;
        }
    }

    .projectDetails {
        border: none!important;
        [class*="body"] {
            margin: 10px 0!important;
            gap: 10px;
        }
        &.blue :local(.row) {
            border: 1px solid $blue;
        }
        &.green {
            :local(.row) {
                background-color: $lightGreen;
            }
        }
        &.gray {
            :local(.row) {
                background-color: $lightGray;
            }
        }
        &.orange :local(.row) {
            background-color: $lightOrange;
        }
    }
}
