@use 'assets/styles/shared' as *;

.checkboxWrapper {
  .checkboxView {
    display: flex;
    cursor: pointer;
    user-select: none;
    color: $color-black;
    font-size: rem(12px);
    line-height: number(12, 18);
    letter-spacing: -0.5px;

    &.disabled {
      cursor: not-allowed;
    }

    input {
      display: none;
    }

    .box {
      margin-right: 7px;
    }
  }

  &.square {
    input:checked ~ .box {
      border: none;
      background: $color-white;

      &::after {
        opacity: 1;
      }
    }

    .checkboxView {
      --border-radius: 3px;

      .box {
        @include box-size(16px);
        @include min-box-size(16px);
        border-radius: var(--border-radius);
        border: 2px solid $color-oslo-gray-2;
        position: relative;
        transition: background $transition-time ease-in-out;

        &::after {
          content: '';
          background: url('../../../../assets/icons/tick.svg') center no-repeat;
          border-radius: var(--border-radius);
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
        }
      }

      &.orange {
        .box {
          border-color: $color-clementine;
        }
      }
    }
  }

  &.round {
    input:checked ~ .box > div {
      opacity: 1;
    }

    .checkboxView {
      align-items: center;

      .box {
        @include box-size(18px);
        @include min-box-size(18px);
        @include f-center;
        border-radius: 50%;
        border: 1px solid $color-clementine;
        transition: opacity $transition-time ease-in-out;
        margin-top: -2px;

        > div {
          background-color: $color-clementine;
          @include box-size(12px);
          border-radius: 50%;
          opacity: 0;
          transition: opacity $transition-time ease;
        }
      }

      &.orange {
        .box {
          border-color: $color-clementine;
        }
      }
    }
  }
}
