@use 'assets/styles/shared' as *;

$margin-left: rem(12px);

.title {
  @include pageTitle;
  margin-left: $margin-left;
}

.content {
  display: flex;

  .tableContainer {
    height: 50vh;

    margin-right: rem(26px);

    .tableWidgetBody {
      margin: rem(14px) rem(18px);
    }
  }

  .graphContainer {
    height: 50vh;

    .graphWidgetBody {
      margin: rem(14px) rem(6px);
    }
  }
}
