@use 'assets/styles/shared' as *;

.counterWrap {
    padding: 1em 0;
    display: flex;
    gap: 10px;
    .counter {
        display: flex;
        flex-direction: column;
        .counterLabel {
            color: $color-fun-blue;
            font-size: rem(16px);
            font-weight: 600;
          }
          .counterValue {
            color: $color-fun-blue;
            font-size: rem(14px);
            font-weight: 600;
          }
    }
}
.exportBtn {
  display: flex;
  justify-self: flex-end;
}