@use 'assets/styles/shared' as *;

.title {
  font-weight: bold;
  font-size: rem(12px);
  line-height: number(12, 16);
  color: $color-fun-blue;
  margin-bottom: rem(8px);
}

.bodyText {
  font-size: rem(12px);
  line-height: number(12, 16);
  color: $color-outer-space;
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;

  &.small {
    font-size: rem(10px);
  }
}


.date {
  margin-top: rem(8px);
  font-size: rem(12px);
  line-height: number(12, 16);
  color: $color-gray;
}

.read {
  position: absolute;
  top: 3px;
  right: 9px;
}

.contentItem {
  background: $color-wild-sand;
  border-radius: $radius-medium;
  padding: rem(16px);
  margin-right: rem(16px);
  width: rem(150px);
  height: 100%;
  position: relative;

  .content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // margin-bottom: rem(8px);
    height: 100%;
  }
}

.bottomControls {
  margin-top: auto;
  display: flex;
  align-items: center;
  
  .more {
    font-weight: 600;
    font-size: rem(12px);
    line-height: number(12, 16);
    margin-left: auto;
    margin-bottom: 0;
    color: $color-fun-blue;
    display: flex;
    align-items: center;

    svg {
      margin-left: 10px;
    }
  }
}
