@use 'assets/styles/shared' as *;

.leftMenu {
  @include f-column;
  position: fixed;
  z-index: 100;
  width: $left-menu-width;
  height: 100vh;
  justify-content: space-between;
  background-color: $color-fun-blue;
  box-shadow: $shadow-blur2-10;
  &.expanded {
    width: rem(278px);
  }

  .versionContainer {
    @include f-center;
    margin-bottom: rem(22px);
    color: $color-white;
    opacity: 0.7;
    &:hover{
      opacity: 1;
    }
  }

  .avatarWrapper {
    @include f-center;
    margin-bottom: rem(22px);

    &.expanded {
      justify-content: space-around;
    }

    .avatar {
      @include circle(48px);
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    .logoutContainer {
      display: flex;

      &:hover {
        .logoutText {
          opacity: 1;
        }

        path {
          opacity: 1;
        }
      }

      .logoutText {
        font-size: rem(14px);
        line-height: number(14, 20);
        letter-spacing: -0.5px;
        margin-left: rem(16px);
        color: $color-white;
        opacity: 0.7;
      }
    }
  }
}
