@use 'assets/styles/shared' as *;
@use "sass:color";

.button {
  transition: all 0.3s ease;
  
  &.fulfilled, &.text, &.outlined {
    display: flex;
    align-items: center;
  }

  &.fulfilled {
    @include blueButton;
  }

  &.orange {
    @include orangeButton;
  }

  &.outlined {
    background-color: unset;
  }

  &.text {
    background-color: unset!important;
    border: none;
    padding: 0;
    color: $color-fun-blue;
    font-weight: 600;
    font-size: rem(12px);

    &:hover {
      color: $color-fun-blue;
      opacity: 0.8;
    }
  }

  &:disabled {
    background-color: $color-silver;
    border-color: $color-silver;

    &:hover {
      background-color: $color-silver;
    }
  }
}

.zoom {
  position: absolute;
  top: calc(50% - 32px);
  left: calc(50% - 32px);
  cursor: pointer;
  z-index: 2;

  &:hover {
    path {
      opacity: 0.6;
    }
    rect {
      fill: $color-silver-darker;
    }
  }

  &:active {
    path {
      opacity: 0.8;
    }
    rect {
      fill: $color-gray-chateau;
    }
  }
}
