@use 'assets/styles/shared' as *;

.form {
  max-width: rem(350px);
  margin-left: rem(-4px);

  .textInput {
    max-width: rem(230px);
  }

  .inputWithCheckbox {
    display: flex;
    align-items: center;

    .checkbox {
      white-space: nowrap;
      margin-right: rem(-50px);
      margin-left: rem(10px);
      margin-bottom: rem(12px);
    }
  }

  .footerButtons {
    display: flex;

    .button {
      @include blueButton;
    }

    .buttonPrimary {
      @include orangeButton;
      margin-left: rem(16px);
    }
  }
}
