@use 'assets/styles/shared' as *;

@mixin cancel-button {
  height: 10px;
  width: 10px;
  margin: 4px;
  margin-right: 10px;
  background: url('../../../../assets/icons/cancelThin.svg') center no-repeat;
  background-size: cover;
}

.form {
  display: flex;
  margin-top: rem(8px);
  flex: 1;
  justify-content: flex-end;

  .input {
    transition: all 0.3s;
    border: none;
    outline: none;
    margin-left: rem(18px);
    font-family: $font-primary;
    font-size: rem(16px);
    line-height: number(16, 24);
    letter-spacing: 0.3px;
    width: 5rem;
    background: transparent;

    &:focus,
    &:active {
      width: 50%;
    }

    &::placeholder {
      color: $color-oslo-gray;
    }

    &::-webkit-search-cancel-button {
      @include cancel-button;
      -webkit-appearance: none;
    }

    //for IE10
    &::-ms-clear {
      @include cancel-button;
    }

    &Disabled {
      &:focus,
      &:active {
        width: 5rem;
      }
    }
  }

  .searchButton {
    display: flex;
    align-items: center;
  }
}
