@use 'assets/styles/shared' as *;
@use "sass:color";

.controlsWrapper {
  margin-left: rem(12px);
  display: flex;
  justify-content: space-between;

  .controls {
    display: flex;
    margin-top: rem(10px);
  }
}

.navigation {
  margin: rem(40px) rem(30px) rem(8px) rem(10px);
  background: $color-wild-sand;
  padding: rem(8px) rem(10px);

  .navItem {
    font-size: rem(12px);
    line-height: rem(16px);
    letter-spacing: 0.01em;
    color: $color-fun-blue;
    margin-right: rem(26px);

    &.selected {
      font-size: rem(12px);
      line-height: rem(16px);
      letter-spacing: 0.01em;
      text-decoration-line: underline;
      color: $color-clementine;
    }
  }
}

.title {
  @include pageTitle;
  margin-left: rem(12px);
}

.subtitle {
  margin-left: rem(12px);
  margin-bottom: rem(8px);
  font-weight: 600;
  font-size: rem(18px);
  line-height: number(18, 25);
  color: $color-fun-blue;
}
