@use 'assets/styles/shared' as *;
@use "sass:color";

.taskListWrap {
  display: flex;
  flex-direction: column;
  margin: 0 32px;
}

.titleWrap {
  display: flex;
  justify-content: space-between;
  column-gap: 26px;
  font-size: 12px;
  align-items: center;

  [class~='MuiButton-root'] {
    color: $color-azure;
    font-size: 12px;
  }
}

.taskList {
  padding: 0;
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  height: 700px;
  overflow: auto;

  [class~='MuiFormControlLabel-root'] {
    margin: 0;
  }

  [class~='MuiFormControlLabel-label'] {
    font-size: 12px;
  }

  [class~='MuiSvgIcon-root'] {
    font-size: 18px;
    color: $color-clementine;
  }
}
