@use 'assets/styles/shared' as *;

.title {
  @include pageTitle;
  margin-left: rem(12px);
}

.menuItemsRow {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  margin-left: rem(12px);
  margin-top: rem(10px);
}

.filterAndTableWrapper {
  width: 100%;
  max-width: 100%;
  padding-right: rem(30px);

  &.tableOnly .filterRow {
    margin-top: rem(8px);
  }

  .filterRow {
    margin: rem(40px) 0 rem(8px) rem(10px);
    display: flex;
    justify-content: space-between;
    background: $color-wild-sand;
    padding: rem(8px) rem(10px);

    .filterItem {
      font-size: rem(12px);
      line-height: rem(16px);
      letter-spacing: 0.01em;
      color: $color-fun-blue;
      margin-right: rem(26px);

      &.selected {
        font-size: rem(12px);
        line-height: rem(16px);
        letter-spacing: 0.01em;
        text-decoration-line: underline;
        color: $color-clementine;
      }
    }

    .search {
      margin: 0;
    }
  }

  .table {
    margin-left: rem(12px);
  }
  .loader {
    margin-top: 2em;
  }
}

.modal {
  margin-top: rem(20px);
  margin-bottom: rem(22px);
}

@media print {
  .menuItemsRow {
    display: none;
  }
  @page {
    margin: 0.5cm;
  }
  [class*="react-contexify"],
  [class*="header"],
  [class*="footer"],
  [class*="rightMenu"],
  [class*="leftMenu"] {
    display: none;
  }
  [class*="styles_th"] {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    &:last-child {
      flex-grow: 0;
      flex-basis: 30px;
    }
  }
  [class*="content"],
  [class*="searchAndTableWrapper"] {
    width: 20cm!important;
  }
}
