@use './assets/styles/shared.scss' as *;

.react-select-container {
  margin-bottom: rem(14px);
  position: relative;

  .filled {
    .react-select {
      &__control {
        border-color: $color-black;
      }
      &__control--is-disabled .react-select__single-value {
        color: rgba(0, 0, 0, 0.6);
      }
      &__value-container {
        &.react-select__value-container--is-multi {
          padding: rem(6px) rem(2px);

          .react-select__placeholder {
            margin-left: rem(10px);
          }
        }
      }
    }
  }

  &.error {
    .react-select {
      &__control {
        border-color: $color-pomegranate;

        .react-select__placeholder {
          color: $color-pomegranate;
        }
      }
    }
  }

  .react-select {
    &__control {
      cursor: pointer;
      border-radius: $tiny-radius;
      border: 1px solid $color-alto-90;

      &:hover {
        border-color: $color-black;
      }

      &.react-select__control--is-focused {
        border-color: $color-clementine;
        outline: none;
        box-shadow: none;
      }

      .react-select__placeholder {
        color: $color-dusty-gray;
        font-style: normal;
        font-weight: normal;
        font-size: rem(16px);
        line-height: number(16, 20);
      }

      .react-select__multi-value {
        padding: rem(7px);
        background: $color-tuft-bush;
        border-radius: 4px;

        &__label {
          @include f-center;
          font-weight: 500;
          font-size: rem(14px);
          line-height: number(14, 14);
          color: $color-clementine;
        }

        &__remove {
          color: $color-clementine;
          font-size: rem(18px);
        }
      }
    }

    &__value-container {
      font-size: rem(16px);
      line-height: number(16, 20);
      font-weight: 400;

      &.react-select__value-container--is-multi {
        padding: rem(2px);
        padding-left: rem(10px);

        .react-select__placeholder {
          margin-left: rem(2px);
        }
      }
    }

    &__indicator-separator {
      display: none;
    }

    &__menu {
      border-radius: $radius-medium;
      z-index: 2;

      .react-select__option {
        &--is-focused {
          background: $color-half-clementine;
        }

        &--is-selected {
          background: $color-clementine;
        }
      }
    }

    &__indicator {
      &.react-select__dropdown-indicator {
        padding: rem(11px);
        padding-left: rem(8px);
        color: $color-clementine;
      }
      &.react-select__clear-indicator {
        color: $color-clementine;
      }
    }
  }

  &.search {
    margin-bottom: 0;

    &:focus-within {
      width: 50%;
    }

    .react-select {
      &__control {
        border: none;
        width: 5rem;
        margin-left: auto;
        margin-right: rem(20px);

        &.react-select__control--is-focused {
          width: 100%;
        }
      }

      &__dropdown-indicator {
        display: none;
      }
    }
  }

  .withLabel {
    .react-select__control {
      border-color: transparent;
    }

    .react-select__placeholder {
      display: none;
    }
  }
  .react-select__menu {
    border-radius: $tiny-radius;
  }
}
