@use 'assets/styles/shared' as *;

.header {
  position: fixed;
  width: 100%;
  height: $header-height;
  z-index: 1;
  display: flex;
  flex-direction: column;
  padding-left: $left-menu-width;
  padding-right: $right-menu-width;
  background: $color-white;

  .headerContent {
    @include f-center-between;

    .logo {
      width: 5rem;
      margin-left: rem(32px);
      margin-top: rem(2px);
      margin-right: rem(8px);
    }

    .searchWrapper {
      align-self: flex-end;
      margin-bottom: rem(8px);
      flex: 1;
    }
  }

  .divider {
    display: flex;
    align-self: flex-end;
    margin-top: auto;
    height: 2px;
    background-color: $color-wild-sand;
    width: 100%;
    position: relative;
    z-index: -1;
  }
}
