@use "sass:selector";
@use 'assets/styles/shared' as *;

.upload {
  @include orangeButton;
  padding-top: 5px;
  padding-bottom: 5px;
  align-items: center;
  display: flex;
}

.mainImageInputWrapper {
  position: relative;

  .cropButton {
    position: absolute;
    bottom: 12px;
    right: 12px;
    z-index: 1;
  }
}

.dndContainer {
  height: 300px;
  border: 1px solid rgba(208, 208, 208, 0.9);
  border-radius: 4px;
  background: #fff;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0.3;
    z-index: -1;
  }

  &.active {
    border-color: $color-black;
    background-color: $color-black-05;
  }
}

.dndContainerMultiple {
  height: auto;

  .imageWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 1rem;
  }

  img {
    justify-self: self-start;
    height: 80px;
    width: auto;
    opacity: 1;
    position: unset;
  }
  .upload {
    opacity: 0.5;
    font-size: 2rem;
  }
}

.editor {
  border-radius: 4px;
  border-color: rgba(208, 208, 208, 0.9);
  [class*='k-toolbar'] {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  &.error {
    border-color: $color-pomegranate;
  }
}

.imagesWrapper {
  display: flex;
  gap: 1rem;

  .imageBlock {
    padding: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border: 1px solid rgba(208, 208, 208, 0.9);
    border-radius: 4px;
  }
}

.checkboxes {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;

  &.subitemDetails {
    padding: 0;
  }

  &.categoryItems {
    row-gap: 0 !important; // blame mui
  }
}

.title {
  font-weight: bold;
  font-size: rem(14px);
  line-height: number(14, 20);
  color: $color-fun-blue;
  margin-bottom: 10px;
}

.warningContainer {
  display: flex;
  margin-bottom: .25rem;

  .warning  {
    font-size: rem(12px);
    padding: 5px;
  }
  
  .warningHighlight {
    padding: 5px;
    color:#EE5F00;
    font-size: rem(12px);
    font-weight: bold;
  }
}

.categories {
  margin-top: rem(20px);
}

.categoryCheckboxWrap {
  display: flex;
  align-items: center;

  .arrow {
    margin-left: 4px;
    transition: transform 150ms cubic-bezier(0.4, 0, 0.2, 1);
    transform: rotate(-90deg);

    &.rotated {
      transform: rotate(0deg);
    }
  }
}

.categorySubitems {
  margin: rem(16px) 0 rem(10px) rem(21px);
}

h3.title {
  font-size: rem(12px);
  margin-bottom: 0;
}

.collapse {
  min-height: 32px !important;
}

section {
  margin-bottom: 1rem;
}

// All importants are needed because Mui has bad order of classes
:global {
  .MuiAccordion-root {
    margin: 5px -5px !important;
    box-shadow: none !important;
    border: none !important;
    background: none !important;
    &::before {
      display: none !important;
    }
    &.Mui-expanded {
      background-color: #fafafa !important;
    }
  }

  .MuiAccordionSummary-content {
    margin: 0 !important;
  }

  .MuiAccordionSummary-root {
    padding: 5px !important;
  }

  .MuiAccordionSummary-expandIconWrapper {
    transform: rotate(-90deg) !important;

    &.Mui-expanded {
      transform: rotate(0deg) !important;
    }
  }

  .MuiIconButton-root {
    padding-top: 8px !important;
    padding-bottom: 8px !important;
  }

  .MuiAccordionDetails-root {
    gap: 1rem !important;
    flex-wrap: wrap !important;
  }
}

.formTabs {
  margin: rem(15px) 0;
}

.bodyError {
  font-weight: 400;
  font-size: rem(12px);
  color: #d32f2f;
  margin: 6px 0 14px 14px;
}
