@use 'assets/styles/shared' as *;

.buttonContainer {
  display: flex;
  justify-content: flex-end;
  margin-right: rem(16px);

  .button {
    @include blueButton;
    margin-bottom: 8px;
  }
}

.userInputContainer {
  position: relative;

  .userInputArrow {
    position: absolute;
    padding: rem(8px) rem(14px);
    right: 2px;
    top: 4px;
  }
}

.upload {
  @include orangeButton;
  padding-top: 5px;
  padding-bottom: 5px;
  align-items: center;
  display: flex;
}

.download {
  margin-left: auto;
  path {
    fill: $color-fun-blue;
  }
}

.dndContainer {
  height: 150px;
  border: 1px solid rgba(208, 208, 208, 0.9);
  border-radius: 4px;
  background: #fff;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0.3;
    z-index: -1;
  }

  &.active {
    border-color: $color-black;
    background-color: $color-black-05;
  }

  .hint {
    margin-top: auto;
    font-size: 12px;
    color: $color-gray;
  }
}


.buttonPrimary {
  // @include blueButton;
  // border: none;
  // display: flex;
  // align-items: center;
  margin-right: auto;
  // padding: 0;
  // background: none!important;

  svg {
    margin-right: 10px;
    transform: scale(-1, 1);
    path {
      fill: $color-fun-blue;
    }
  }
  &:hover {
    color: $color-fun-blue;
    opacity: 0.8;
  }
}

.formHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 16px 16px;
  gap: 16px;
}
.progress {
  display: flex;
  align-items: center;
  gap: 30px;

  button {
    display: inline-flex;
    width: 30px;
    height: 30px;
    align-items: center;
    justify-content: center;
    border: 1px solid $color-clementine;
    position: relative;
    border-radius: 50%;

    &.active {
      background-color: $color-clementine;
      color: #fff;
    }
    
    &::after {
      content: '';
      display: block;
      width: 30px;
      height: 1px;
      background-color: $color-clementine;
      position: absolute;
      left: 100%;
    }
    &:last-of-type::after {
      display: none;
    }
  }
}

.stepTitle {
  font-size: 12px;
  margin-bottom: 10px;
  margin-top: 15px;
  color: $color-fun-blue;
}
h2.stepTitle {
  font-size: 14px;
}

.fileName {
  font-size: 12px;
  color: $color-fun-blue;
  display: flex;
  align-items: center;
  // justify-content: space-between;
  gap: 1rem;

  .removeButton {
    font-size: 20px;
  }
}
.fieldTitle {
  font-size: 12px;
  color: #18469E;
}