@use 'assets/styles/shared' as *;
@use 'sass:color';

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 35px;
  height: 100%;

  svg {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
  }

  .title {
    @include pageTitle;
    margin-top: 30px;
  }
}