@use 'assets/styles/shared' as *;

.printContainer {
    display: none;
    @media print {
        display: block;
        size: A4;
    }
}

// prevent page break in the middle of a table
.dataBody {
    page-break-inside: avoid;
}

.detailMainWrap {
    page-break-after: always;
}

.detailHeader, .fieldTitle {
    color: $color-fun-blue;
}

.dataLabel, .dateLabel {
    color: $color-gray;
}

.dataGrid {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    margin-bottom: 1rem;
}

.custom_table {
    width: 1000px;
    border-width: 1px ;
    border-style: solid ;
    border-color: #F5F5F5;
}
.custom_bonus_table {
    max-width: 1000px;
    border-width: 1px ;
    border-style: solid ;
    border-color: #F5F5F5;
}
.table_head {
    padding-left: 4px;
    padding-right: 4px;
    //font-weight: bold;
    text-align: left;
    color: #8E8E8E;
    border-right-width: 1px;
    border-left-width: 1px;
    border-top-width: 0;
    border-bottom-width: 1px;
    border-style: solid;
    border-color: #ddd;
}
.table_head +.table_head{
    border-left: none;
}
.table_data {
   padding-left: 4px;
   padding-right: 4px;
    border-right-width: 1px;
    border-left-width: 1px;
    border-style: solid;
    border-bottom: none;
    border-color: #ddd;
    //color: #8E8E8E;

}
.table_row{
    border-spacing: 0;
    border-bottom-width: 1px;
    border-style: solid;
    border-color: #ddd;
}
.table_data:last-child {
    border-left-width: 0;
}

//.table_body {
//    border-collapse: separate;
//    border-spacing: 0;
//}
/* Style the last column */
.table_data:last-child {
    border-right-width: 0;
}

/* Style the first row */
.table_row:first-child .table_data {
    border-top-width: 0;
}

/* Style the last row */
.table_row:last-child .table_data {
    border-bottom-width: 0;
}
.table_data + .table_data {
    border-left: none;
}
.table_row + .table_row {
    border-bottom: none;
}
