@use 'assets/styles/shared' as *;

.filterContainer {
  min-width: rem(200px);
  min-height: 50vh;
  background: $color-wild-sand;
  border-radius: rem(7px);
  padding: rem(16px) rem(14px);
  margin-left: rem(12px);
  margin-right: rem(12px);

  .containerTitle {
    font-weight: 600;
    font-size: rem(14px);
    line-height: rem(19px);
    letter-spacing: 0.01em;
    color: $color-jackson-blue;
    margin-bottom: rem(20px);
  }

  .sectionTitle {
    font-weight: 600;
    font-size: rem(12px);
    line-height: number(12, 16);
    letter-spacing: 0.01em;
    color: $color-jackson-blue;
    margin-top: rem(16px);
    margin-bottom: rem(6px);
  }

  .checkboxesContainer {
    margin-top: rem(12px);

    .radioBox {
      margin-bottom: rem(14px);

      > label {
        font-size: rem(12px);
        line-height: number(12, 18);
        letter-spacing: -0.5px;
        color: $color-black;

        div {
          border-color: $color-clementine;
        }
      }
    }
  }

  .buttonWrapper {
    display: flex;
    justify-content: space-between;
    margin-top: rem(30px);

    .button {
      @include blueButton;
    }

    .buttonPrimary {
      @include orangeButton;
    }
  }
}
