@use 'assets/styles/shared' as *;

.highlighted {
  font-weight: 600;
}

.button {
  font-size: rem(14px);
  line-height: number(14, 19);
  letter-spacing: 0.01em;
  display: flex;
  justify-content: space-between;
  width: 100%;

  &.selected {
    color: $color-clementine;

    svg path {
      fill: $color-clementine;
    }
  }

  svg {
    margin-left: 30px;
    path {
      fill: $color-fun-blue;
    }
  }
}

.subItemsContainer {
  padding: rem(30px) rem(26px)!important;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
  max-width: rem(300px);
  min-width: 70%;
  width: fit-content;
  position: absolute!important;
  left: calc(100% + 30px);
  bottom: -26px;

  .subItemsContainer {
    top: auto;
    bottom: -30px;
  }
}

.closeButton {
  position: absolute;
  top: 7px;
  right: 7px;
  svg {
    width: 15px;
  }
}