@use 'assets/styles/shared' as *;

.title {
  @include pageTitle;
}

.groupheaderbutton {
  margin-left: 2em;
  margin-top: 1em;
  margin-bottom: 2em;
}

.orangeButton {
  margin: 5px;
  background-color: #f69546;
  color: #203a88;
  border-radius: 5px; /* Adjust the value as needed */
  padding: 0.5em;
  font-weight: 700;
}
.grayButton {
  margin: 5px;
  background-color: gray;
  color: #ffffff;
  border-radius: 5px; /* Adjust the value as needed */
  padding: 0.5em;
  font-weight: 700;
}
.blueHeaderBtn {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 12px;
}
.blueHeader {
  background-color: #203a88;
  color: white;
  border-radius: 5px; /* Adjust the value as needed */
  padding: 0.5em;
  font-weight: 700;
  margin-left: 2.5em;
}
.blueButton {
  margin: 5px;

  background-color: #203a88;
  color: white;
  border-radius: 5px; /* Adjust the value as needed */
  padding: 0.5em;
  font-weight: 700;
}
.dashboard {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: max-content;
  grid-gap: 2rem 1.5rem;
  gap: 2rem 1.5rem;
  height: 100%;

  @media screen and (min-width: 3840px) {
    grid-template-columns: repeat(6, 1fr);
  }

  @media screen and (min-width: 2560px) {
    grid-template-columns: repeat(5, 1fr);
  }

  @media screen and (max-width: 1400px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media screen and (max-width: 1024px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media screen and (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.wrap {
  display: flex;
  padding: 0 35px 15px 35px;
  margin-top: 20px;
  // height: 80%;
  gap: 20px;

  & > div {
    flex: 1;
  }

  &.withArticle {
    .dashboard {
      grid-template-columns: repeat(2, 1fr);

      @media screen and (min-width: 2560px) {
        grid-template-columns: repeat(3, 1fr);
      }

      @media screen and (max-width: 1024px) {
        grid-template-columns: 1fr;
      }
    }
  }
}

.sticky {
  position: sticky;
  top: 0;
  padding: 15px 0;
  background-color: $color-white;
  z-index: 2;
}

.wrapNav {
  display: flex;
  align-items: center;
}

.navigation {
  margin: 0 35px 0;
  padding: 0 15px;
  background-color: $color-wild-sand;
  position: relative;
  display: flex;

  &:not(:last-child) {
    margin-bottom: 15px;
  }

  &.mainNavigation {
    background: $color-white;
    margin-top: 15px;
    margin-bottom: 0;
  }

  .navigationItem {
    background: none;
    color: $color-fun-blue;
    cursor: pointer;
    font-size: rem(12px);
    padding: 5px;
    transition: all 0.3s;

    &:hover,
    &.active,
    &:focus {
      &.gray {
        background-color: $color-dark;
        color: #fff !important;
      }

      &.green {
        background-color: $color-green;
        color: #fff !important;
      }

      &.orange {
        background-color: $color-clementine;
        color: #fff !important;
      }

      &.blue {
        background-color: $color-fun-blue;
        color: #fff !important;
      }
    }

    &.selected {
      color: $color-clementine;
      text-decoration: underline;
    }

    + .navigationItem {
      margin-left: 1rem;
    }
  }

  .filterBtn {
    display: flex;
    margin-left: auto;
    align-items: center;
    .filter {
      font-size: rem(12px);
      padding: 5px;
      color: #c5c7cd;
      font-weight: bold;
      transition: 0.3s;
      &:hover {
        color: #ee5f00;
      }
    }
    .filterActive {
      font-size: rem(12px);
      padding: 5px;
      color: #00389a;
      font-weight: bold;
      transition: 0.3s;
      &:hover {
        color: #ee5f00;
      }
    }
  }
}

.news {
  width: 100%;
  aspect-ratio: 16 / 9;
  background-color: #e5e5e5;
  background-size: cover;
  color: #fff;
  position: relative;
  display: flex;
  border-radius: 7px;
  flex-direction: column;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    display: block;
    background: linear-gradient(0deg, rgb(0 0 0 / 60%) 30%, transparent);
    width: 100%;
    height: 50%;
    bottom: 0;
    left: 0;
    z-index: 0;
  }

  &.mainNews {
    justify-content: flex-end;
    background-color: #fff;
    transition: all 0.3s;

    &:not(.withBg) {
      border-radius: 0;
    }

    &.collapsed {
      position: sticky;
      top: 0;
      grid-row-start: 1;
      grid-row-end: 4;
      height: calc(100vh - 290px);

      h3 {
        color: $color-fun-blue;
        font-weight: 600;
        font-size: rem(18px);
        margin-bottom: 1rem;
      }
      &.withBg {
        background-image: url(../../../assets/images/bg_img.png);
        background-position: center;
      }
    }

    &.withBg {
      background-image: url(../../../assets/images/main_news.png);
      padding: 1rem;
      background-size: cover;

      h3 {
        color: #fff;
        background-color: $color-fun-blue;
        margin: -1rem;
        margin-bottom: 2rem;
        padding: 0.5rem 1rem;
        font-size: rem(14px);
      }
    }
  }

  .animated {
    margin-top: auto;
    border-radius: 7px;
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    transition: all 1s;
    height: 70px;
    z-index: 1;

    .plusButton {
      width: 30px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      background: $color-gray;
      border-radius: 50%;
    }
    &.orange .plusButton {
      background-color: $color-clementine;
    }
    &.green .plusButton {
      background-color: $color-green;
    }
    &.blue .plusButton {
      background-color: $color-jackson-blue;
    }

    > *:not(.plusButton, .newsTitle, .categoryLabel) {
      display: none;
    }
  }

  &:hover {
    .animated {
      animation: height 1s 1;
      height: 200px;
      background-color: transparentize($color-dark, 0.2);

      &.orange {
        background-color: transparentize($color-clementine, 0.2);

        .more svg path {
          fill: #fff;
        }
      }
      &.green {
        background-color: transparentize($color-green, 0.2);
      }
      &.blue {
        background-color: transparentize($color-jackson-blue, 0.2);
      }
      .date,
      .sub {
        display: block;
      }
      .bottomControls,
      .more {
        display: flex;
      }
      .description {
        display: -webkit-box;
      }
      .plusButton {
        display: none;
      }
    }
    .categoryLabel {
      display: none;
    }
  }
}
.categoryLabel {
  position: absolute;
  top: 1.5rem;
  padding: 2px 15px;
  font-size: rem(10px);
}
.date {
  font-size: rem(12px);
}
.newsTitle {
  font-size: rem(14px);
  font-weight: 700;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box !important;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.more {
  text-align: left;
  display: flex;
  align-items: center;
  font-size: rem(14px);
  font-weight: 700;
  margin-bottom: 1rem;
  z-index: 1;

  svg {
    margin-left: 10px;
  }
}
.description {
  font-size: rem(12px);
  margin-bottom: 1rem;
  margin-top: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}
.sub {
  font-size: rem(10px);
}

.bottomControls {
  margin-top: auto;
  display: flex;
  align-items: center;

  .more {
    margin-left: auto;
    margin-bottom: 0;
  }
}

@keyframes height {
  from {
    height: 70px;
  }
  to {
    height: 200px;
  }
}

.newsModal {
  padding: 1rem;
  padding-bottom: 0;
  display: grid;
  grid-template-columns: 275px auto;
  gap: 2rem;

  > div {
    display: flex;
    flex-direction: column;
  }

  .action {
    @include orangeButton;
    margin: 1rem;
    margin-left: auto;
    margin-top: auto;
  }

  .itemsList .action {
    margin-top: 1rem;
  }
}

.items {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding-right: 5px;
  gap: 2rem;
}

.withBg .items {
  margin-right: -1rem;
  padding-right: 1rem;
}

.article {
  background-color: #f5f5f5;
  border-radius: 7px;

  > div {
    margin: 0;
  }
}

.bigItemContainer {
  position: sticky;
  top: 0;
  align-self: flex-start;
  overflow: hidden auto;

  .cancel {
    position: absolute;
    z-index: 2;
    right: 4px;
    top: 0;
  }

  & > div {
    margin-left: rem(27px);
  }

  .loader {
    margin-left: auto;
  }
}

.itemBlock {
  width: 100%;
  margin: 0;
}

.companyInfoModal {
  padding: rem(14px);
}
