@use './assets/styles/shared.scss' as *;

.draggable {
  cursor: move;
}

.static {
  .draggable {
    cursor: not-allowed;
  }
}

.react-grid-layout.layout {
  width: 100%;
  margin-top: -150px;

  .react-grid-item.react-grid-placeholder {
    background: $color-fun-blue;
  }
}

.react-grid-item .swiper-container-horizontal > .swiper-scrollbar {
  width: calc(100% - 90px);

  .swiper-scrollbar-drag {
    background: $color-clementine;
    border-radius: $radius-small;
    height: 4px;
  }
}
