@use "sass:color";
@import './functions';
@import './variables';
@import './animations';

@mixin formTitle {
  margin: rem(10px) rem(16px);
  margin-bottom: 2px;
  font-weight: 600;
  font-size: rem(18px);
  line-height: rem(25px);
  color: $color-fun-blue;
}

@mixin blueButton {
  font-size: rem(12px);
  line-height: number(12, 18);
  text-align: center;
  color: $color-fun-blue;
  padding: rem(8px) rem(16px);
  border: 1px solid $color-jackson-blue;
  box-sizing: border-box;
  border-radius: 4px;

  &:hover {
    background-color: $color-fun-blue;
    color: $color-white;
  }

  &:active {
    background: color.scale($color-fun-blue, $lightness: 15%);
  }
}

@mixin orangeButton {
  @include blueButton;
  font-weight: 600;
  color: $color-white;
  background: $color-clementine;
  border-color: $color-clementine;

  &:hover {
    background: color.scale($color-clementine, $lightness: 15%);
  }
  &:active {
    background: color.scale($color-clementine, $lightness: 5%);
  }
}

@mixin pageTitle {
  font-weight: 600;
  font-size: rem(24px);
  line-height: number(24, 34);
  letter-spacing: 0.01em;
  color: $color-jackson-blue;
  margin-top: rem(30px);
  margin-left: rem(34px);
}
