@use './assets/styles/shared.scss' as *;

.sharedDatepicker {
  .DayPicker-NavButton--prev {
    background-image: url('../icons/calendarPrev.svg');
  }
  .DayPicker-NavButton--next {
    background-image: url('../icons/calendarNext.svg');
    margin-right: -10px;
  }
  .DayPicker-Caption {
    font-family: 'Open Sans';
    font-style: normal;
    color: $color-fun-blue;
  }
  .DayPicker-Weekday {
    font-family: 'Open Sans';
    font-style: normal;
    text-align: center;
  }
  .DayPicker-Day {
    font-family: 'Open Sans';
    font-style: normal;
    text-align: center;
    color: $color-silver-2;
  }
  .DayPicker-Day.DayPicker-Day--today {
    font-family: 'Open Sans';
    font-style: normal;
    text-align: center;
    color: $color-azure;
  }
  .DayPicker-Day.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
    font-family: 'Open Sans';
    font-style: normal;
    background-color: $color-clementine;
    color: $color-white;
    text-align: center;
  }
}

.customDatepicker {
  @extend .sharedDatepicker;

  .DayPickerInput {
    width: 100%;

    > input {
      background: $color-white;
      border: 1px solid $color-alto-90;
      box-sizing: border-box;
      border-radius: 4px;
      outline: none;
      width: 100%;
      padding: 13px;

      &::placeholder {
        color: $color-dusty-gray;
        opacity: 1;
      }

      &:hover {
        border-color: $color-black;
      }

      &.focus-visible {
        border-color: $color-clementine;
      }

      &.filled {
        border-color: $color-black;
      }
    }
  }
  .DayPicker-NavButton {
    top: 0.8em;
    background-size: 35%;
  }
  .DayPickerInput-Overlay {
    z-index: 2;
  }
  .DayPicker-Caption {
    font-weight: normal;
    font-size: rem(14px);
    line-height: number(14, 16);
    letter-spacing: -0.24px;
  }
  .DayPicker-Weekday {
    font-weight: 500;
    font-size: rem(12px);
    line-height: number(12, 14);
    letter-spacing: -0.17px;
    color: $silver-chalice;
  }
  .DayPicker-Day {
    font-weight: 500;
    font-size: rem(12px);
    line-height: number(12, 16);
    letter-spacing: -0.17px;
    border-radius: 3px;
  }
  .DayPicker-Day.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
    font-weight: 500;
    letter-spacing: -0.17px;
  }
  .DayPicker-Day.DayPicker-Day--today {
    font-weight: 500;
    letter-spacing: -0.17px;
  }
}

.rightMenuDatepicker {
  @extend .sharedDatepicker;
  display: flex;
  justify-content: center;
  height: rem(310px);

  .DayPicker-Caption {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: -0.4px;
  }
  .DayPicker-Weekday {
    color: $color-woodsmoke-42;
    font-size: rem(16px);
    line-height: number(16, 20);
    text-align: center;
    letter-spacing: -0.3px;
  }
  .DayPicker-Day {
    font-size: rem(16px);
    line-height: number(16, 20);
    letter-spacing: -0.3px;
    border-radius: 4px;
  }
  .DayPicker-Day.DayPicker-Day--today {
    font-weight: normal;
    letter-spacing: -0.3px;
  }
  .DayPicker-Day.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
    font-weight: normal;
    letter-spacing: -0.3px;
  }
}
