@use "sass:color";
@use 'assets/styles/shared' as *;

.notifyWrap {
  display: flex;
  flex-direction: column;
  position: absolute;
  row-gap: 5px;
  right: 0;
  top: 20px;
  width: 215px;
  z-index: 100;
  transform: translateX(100%);
  background-color: $color-wild-sand;
  padding: 7px;
  box-shadow: 0px 4px 4px rgba(124, 120, 120, 0.25);
  border-radius: 7px;
  transition: all 0.3s linear;

  &.active {
    transform: translateX(-50%);
  }
}

.notifyTitleWrap {
  display: flex;
  justify-content: space-between;
  align-items: center;

  h4 {
    font-size: 12px;
  }
  span {
    font-size: 6px;
  }
}

.notifyText {
  font-size: 11px;
}

.label {
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
  text-align: left;

  &:hover {
    cursor: pointer;
    text-decoration: underline;
    text-decoration-color: #18469e;
  }
}
