@use 'assets/styles/shared' as *;

.popupBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(220, 10%, 89%, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 150;
}

.popupWrap {
  width: 100%;
  max-width: 382px;
  background-color: $color-white;
  padding: 16px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  row-gap: 16px;

  h5 {
    margin: 0;
    font-size: 18px;
    color: $color-fun-blue;
  }

  p {
    font-size: 12px;
  }
}

.rangeWrap {
  display: flex;
  flex-direction: column;
}

.greyText {
  font-weight: 400;
  font-size: 12px;
  color: $color-dusty-gray;
}

.numberText {
  font-weight: 400;
  font-size: 14px;
  display: flex;
  flex-direction: column;
}

.buttonsWrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 24px;

  button {
    font-size: 12px;
    font-weight: 400;
    white-space: nowrap;
  }
}

.buttonAccept {
  &[class~='MuiButtonBase-root'] {
    background-color: $color-clementine;

    &:hover {
      background-color: $color-fire;
    }
  }
}

.buttonRight {
  &[class~='MuiButtonBase-root'] {
    margin: 0 0 0 auto;
  }
}
