@use 'assets/styles/shared' as *;

.subItem {
  margin-left: rem(10px);
  margin-top: rem(14px);

  .title {
    font-size: rem(14px);
    line-height: number(14, 20);
    letter-spacing: 0.01em;
    color: $color-white;
    opacity: 0.5;

    &.expanded {
      opacity: 1;
    }

    &.selected {
      font-weight: 600;
      color: $color-clementine;
      opacity: 1;
    }
  }
}
