@use 'assets/styles/shared' as *;

.navigation {
  display: flex;
  align-items: center;
  margin: 0 rem(40px);
  padding: rem(20px) 0;
  overflow: auto;
  

  .navigationItem {
    color: #D9D9D9;
    position: relative;
    margin-right: 8px;

    .button {
      width: rem(50px);
      height: rem(50px);
      background-color: currentColor;
      transition: background-color 0.3s;
      padding: 4px;

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    &.selected {
      color: $color-clementine;
    }

    &:hover {
      color: $color-clementine;

      .itemTitle {
        display: block;
      }
    }

    .initials {
      color: $color-white;
    }

    .itemTitle {
      display: none;
      position: absolute;
      left: 50%;
      bottom: -20px;
      padding-top: 20px;
      transform: translateX(-50%);
      font-size: 12px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .deleteItem {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(50%, -50%);
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: #C4C4C4;;
      color: $color-white;
    }
  }

  .addItem {
    height: 50px;
    background-color: #F2F2F2;
    flex-grow: 1;
    padding-left: rem(20px);
    display: flex;
    align-items: center;
    margin-right: 8px;

    .addItemButton {
      font-size: 14px;
    }
  }
}
