@use 'assets/styles/shared' as *;

.commentEditor {
  position: absolute;
  bottom: rem(-24px);
  min-height: rem(130px);
  width: 100%;
  margin-top: rem(50px);
  display: flex;
  background: $color-wild-sand;
  border: 2px solid $color-white;
  box-sizing: border-box;
  box-shadow: 0px rem(4px) rem(20px) rem(6px) rgba(0, 0, 0, 0.25);
  border-radius: rem(8px);

  .avatar {
    @include circle(38px);
    @include f-center;
    background: $color-white;
    overflow: hidden;
    margin: rem(6px);
    margin-top: rem(14px);
  }
  .textareaWrapper {
    display: flex;
    flex: 1;
    margin-right: rem(14px);
    margin-left: rem(10px);

    .textarea {
      margin-top: rem(14px);
      margin-bottom: rem(10px);
      width: 100%;
    }
  }

  .buttonsWrapper {
    display: flex;
    align-items: flex-end;
    margin-bottom: rem(10px);

    .blueButton {
      @include blueButton;
      margin-right: rem(16px);
    }

    .orangeButton {
      @include orangeButton;
      margin-right: rem(8px);
    }
  }
}
