@use 'assets/styles/shared' as *;

.overlay {
  @include f-center;
  height: 100vh;
  width: 100vw;
  padding: 0 rem(32px) rem(8px);
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  background: rgba($color-iron, 0.7);
}

.closeButton {
  position: absolute;
  top: 10px;
  z-index: 10;
  right: 10px;
  svg rect {
    fill: $color-fun-blue
  }
}

.modal {
  @include f-column;
  width: 100%;
  max-height: 100%;
  max-width: rem(400px);
  border-radius: $radius-medium;
  background: $color-white;
  outline: none;
  overflow: hidden;
  position: relative;
  border: 2px solid $color-wild-sand;

  .modalContent {
    flex: 1 1;
    max-height: 100%;
    overflow: auto;
    padding-bottom: rem(20px);
  }
}

@include media($tablet) {
  .overlay {
    padding: 0;
  }

  .modal {
    border-radius: 0;
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
  }
}
