@use 'assets/styles/shared' as *;
@use "sass:color";

.button {
  @include blueButton;
}

.title {
  @include pageTitle;
  margin-left: rem(12px);
}

.controls {
  margin-left: rem(12px);
  display: flex;
  justify-content: space-between;

  .navigation {
    display: flex;
    margin-top: rem(10px);
  }
}

.searchAndTableWrapper {
  width: fit-content;
  padding-right: rem(30px);

  .search {
    margin: rem(34px) 0 rem(10px) 0;
  }

  .table {
    margin-left: rem(12px);
  }
}

.modal {
  margin-top: rem(20px);
  margin-bottom: rem(22px);
}
