@use 'assets/styles/shared' as *;

.item {
  display: flex;
  align-items: center;

  .text {
    font-weight: 600;
    font-size: rem(12px);
    line-height: number(12, 16);
    color: $color-fun-blue;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    user-select: none;
  }

  .arrowWrapper {
    @include f-center;

    svg {
      margin-left: 6px;
    }
  }
}
