@use './assets/styles/shared' as *;
@use "sass:color";
// @import './assets/styles/react-select.scss';
@import './assets/styles/react-grid-layout.scss';
@import './assets/styles/swiper.scss';
@import './assets/styles/react-modal.scss';
@import './assets/styles/daypicker.scss';
@import './assets/styles/tippy.scss';
@import './assets/styles/react-contexify.scss';
@import './assets/styles/footerSliderStyles.scss';

$transition-time: 0.3s;

html,
body {
  --font-size: 16px;
  overflow: hidden;
  padding: 0;
  margin: 0;
  font-size: var(--font-size);
  // font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  //   'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  //   sans-serif;
  font-family: $font-primary;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;

  /* width */
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: $color-alto;
    border-radius: $radius-small;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: $color-clementine;
    border-radius: $radius-small;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: color.scale($color-clementine, $lightness: -15%);
  }
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
}

button,
.button {
  @include button;
}

.blur-container {
  transition: filter $transition-time linear;

  &.blurred {
    filter: blur(2px);
    pointer-events: none;
  }
}

@include media(1200px) {
  html,
  body {
    font-size: calc(var(--font-size) - 1px);
  }
}

@include media(960px) {
  html,
  body {
    font-size: calc(var(--font-size) - 1px);
  }
}

@include media(768px) {
  html,
  body {
    font-size: calc(var(--font-size) - 2px);
  }
}

@include media(500px) {
  html,
  body {
    font-size: calc(var(--font-size) - 3px);
  }
}
