@use 'assets/styles/shared' as *;
@use 'sass:color';


.iframeWrap {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  border:none;
  overflow: hidden;

  #dokuwiki__header{
    display: none !important;
  }
  .title {
    @include pageTitle;
  }

  .iframe {
    margin: rem(32px);
    flex: 1;
    border: none;
  }
}

/****************/
.wiki {
  margin-right: 15%;

  .titleWrapper {
    display: flex;
    justify-content: space-between;

    .title {
      @include pageTitle;
      margin-left: rem(25px);;
    }

    .controlsWrapper {
      display: flex;
      justify-content: flex-end;
      margin-top: rem(18px);

      .link {
        font-size: rem(12px);
        line-height: number(12, 16);
        margin-left: rem(16px);
        color: $color-fun-blue;

        &:hover {
          color: color.scale($color-fun-blue, $lightness: 25%);
        }

        &:active {
          color: color.scale($color-fun-blue, $lightness: -15%);
        }
      }
    }
  }

  .content {
    display: flex;

    .menu {
      margin-left: rem(12px);
      margin-right: rem(8px);
      display: flex;
      flex-direction: column;
      min-width: rem(210px);
    }

    .article {
      border-radius: 7px;
      padding: rem(16px);
      width: 100%;


      .header {
        font-size: rem(24px);
        line-height: number(24, 33);
        letter-spacing: 0.01em;
        color: $color-fun-blue;
        margin-bottom: rem(7px);
      }

      .text {
        font-size: rem(12px);
        line-height: number(12, 16);
        color: $color-black;
        margin-bottom: rem(12px);
      }

      .subHeader {
        font-weight: 600;
        font-size: rem(18px);
        line-height: number(18, 25);
        letter-spacing: 0.01em;
        color: $color-jackson-blue;
        margin-top: rem(4px);
        margin-bottom: rem(8px);
      }

      .caption {
        font-weight: bold;
        font-size: rem(18px);
        line-height: number(18, 25);
        letter-spacing: 0.01em;
        color: $color-fun-blue;
        margin-bottom: rem(8px);
      }

      .search {
        background: #F5F5F5;
        margin: 40px 0 24px;
      }

      .list {
        margin: 11px 0 24px 0;

        ol {
          margin: 0;
          font-weight: 600;
          font-size: 12px;
          line-height: 16px;
          letter-spacing: 0.01em;
          color: #203A88;
          padding: 0;
          list-style: none;
          counter-reset: li;

          .subNumeric {
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            letter-spacing: -0.5px;
            padding: 5px 10px;
          }
        }

        li:before {
          counter-increment: li;
          content: counters(li, ".") " ";
        }
      }
    }
  }
}
