@use 'assets/styles/shared' as *;

.comments {
  margin: rem(16px);
  margin-bottom: 0;
  padding-bottom: rem(16px);
  position: relative;
  overflow: hidden;

  &.editing {
    overflow: visible;
  }

  .title {
    font-weight: 600;
    font-size: rem(18px);
    line-height: rem(26px);
    color: $color-fun-blue;
    margin-bottom: rem(12px);
  }

  .commentEditor {
    transition: bottom 0.3s;
    bottom: rem(-225px);

    &.editing {
      bottom: 0px;
      position: sticky;
    }
  }
}

.loader {
  padding-top: 1em !important;
}

.noComments {
  padding-top: 2em;
  font-weight: 600;
  text-align: center;
  font-size: rem(16px);
  line-height: rem(21px);
  color: $color-fun-blue;
  margin-bottom: rem(12px);
}
