@use 'assets/styles/shared' as *;

.item {
  background: $color-wild-sand;
  border-radius: $radius-medium;
  overflow: hidden;
  margin-right: rem(24px);
  padding-bottom: rem(40px);

  .blogImage {
    width: 100%;
    height: auto;
  }

  .mainImageWrapper > button {
    width: 100%;
  }

  .titleWrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .title {
    font-weight: bold;
    font-size: rem(18px);
    line-height: number(18, 26);
    color: $color-fun-blue;
  }

  .author {
    font-size: rem(12px);
    line-height: number(12, 20);
    color: $color-jackson-blue;
    margin-bottom: rem(20px);
  }

  .date {
    font-size: rem(12px);
    line-height: number(12, 16);
    color: $color-gray;
    margin-bottom: 4px;
  }

  .content {
    margin-top: rem(18px);
    margin-left: rem(32px);
    margin-right: rem(34px);

    // To prevent overflowing because of inline widths
    * {
      max-width: 100% !important;
    }
  }

  .images {
    display: flex;
    margin-top: rem(10px);
    width: inherit;
    flex-wrap: wrap;
    img {
      width: rem(160px);
      height: rem(88px);
      object-fit: cover;
      margin: rem(12px) rem(10px) 0 0;
    }
  }

  a {
    color: $color-fun-blue;

    &:active,
    &:focus,
    &:hover {
      color: $color-clementine;
      text-decoration: underline;
    }
  }

  .readButton {
    margin: rem(10px) 0 0 auto;
  }
}
