// px to rem
@function rem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}

@mixin f-column {
  display: flex;
  flex-direction: column;
}

@mixin f-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin f-center-start {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

@mixin f-center-end {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

@mixin f-center-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin f-center-around {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

@mixin media($max) {
  @media screen and (max-width: $max) {
    @content;
  }
}

@mixin ellipsis($lineCount) {
  @if $lineCount == 1 {
    display: block;
    white-space: nowrap;
  } @else {
    display: -webkit-box;
  }

  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $lineCount;
}

@function number($fontSize, $lineHeight) {
  $lineHeightNumber: $lineHeight / $fontSize;
  @return $lineHeightNumber;
}

@mixin box-size($size) {
  height: $size;
  width: $size;
}

@mixin circle($size) {
  @include box-size($size);
  border-radius: 50%;
}

@mixin min-box-size($size) {
  height: $size;
  width: $size;
}

@mixin box-size($size) {
  height: $size;
  width: $size;
}

@mixin min-box-size($size) {
  min-height: $size;
  min-width: $size;
}

@mixin max-box-size($size) {
  max-height: $size;
  max-width: $size;
}

@mixin button {
  background: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
  font-size: rem(16px);
  line-height: number(16, 20);

  &:focus {
    outline: none;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.7;
    transform: none;
  }

  &.loading {
    opacity: 0.7;
    pointer-events: none;
  }
}
