@use 'assets/styles/shared' as *;

.title {
  font-weight: bold;
  font-size: rem(14px);
  line-height: number(14, 18);
  letter-spacing: 0.25px;
  color: $color-charade;
}

.period {
  font-size: rem(10px);
  line-height: number(10, 12);
  letter-spacing: 0.063px;
  color: $color-santas-gray;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-right: rem(32px);
  margin-bottom: rem(36px);

  .legend {
    display: flex;
    align-self: flex-end;

    .legendTextWrapper {
      display: flex;
      align-items: center;

      font-weight: 600;
      font-size: rem(10px);
      line-height: rem(12px);
      letter-spacing: 0.063px;
      color: $color-outer-space;

      .colorBar {
        width: rem(12px);
        height: 2px;
        margin-right: rem(5px);
      }
    }
  }
}

.statistic {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: rem(8px) rem(22px) 0;

  > div {
    width: 50%;
    margin-bottom: rem(36px);
  }

  .statisticTitle {
    font-weight: 600;
    font-size: rem(12px);
    line-height: number(12, 22);
    letter-spacing: 0.3px;
    color: $color-santas-gray;
  }

  .statisticData {
    font-weight: 600;
    font-size: rem(14px);
    line-height: number(14, 19);
    letter-spacing: 0.3px;
    color: $color-jackson-blue;
  }
}
