@use 'assets/styles/shared' as *;

.title {
  @include formTitle;
}

.filterAndTableWrapper {
  width: fit-content;

  .filterWrapper {
    border-bottom: 2px solid $color-wild-sand;
    margin-bottom: rem(5px);
  }

  .table {
    margin: 1px;
  }
}

.buttonsContainer {
  display: flex;
  justify-content: space-between;
  margin-top: rem(54px);

  .blueButton {
    @include blueButton;
    margin-left: rem(16px);
  }

  .orangeButton {
    @include orangeButton;
    margin-left: rem(16px);
    margin-right: rem(22px);
  }
}
