@use 'assets/styles/shared' as *;

.process {
  position: relative;
  margin-top: rem(16px);
  max-height: calc(100% - 50px); // 50px - header height

  img {
    height: 100%;
  }

  .zoom {
    position: absolute;
    top: calc(50% - 32px);
    left: calc(50% - 32px);
    cursor: pointer;

    &:hover {
      path {
        opacity: 0.6;
      }
      rect {
        fill: $color-silver-darker;
      }
    }

    &:active {
      path {
        opacity: 0.8;
      }
      rect {
        fill: $color-gray-chateau;
      }
    }
  }
}
